import { Resource } from "./Resource";
import { Framebuffer } from "./Framebuffer";
import { Mesh } from "./Mesh";
import { ResourceFactory } from "../utils/ResourceFactory";
import { WebGL2 } from "../WebGL2";
import { MeshGenerator } from "../utils/MeshGenerator";
import vec3 from "../utils/tsm/vec3";
import { GLHelper } from "../utils/GLHelper";
import { UniformBuffer } from "./UniformBuffer";
import { StructEntry, DataTypes } from "../utils/StructBuffer";
import vec4 from "../utils/tsm/vec4";

export class ComputeWrapper extends Resource {
    private mTextureHandle : WebGLTexture;
    private mTexSize : vec3;
    private mComputeIt : number;
    private mFramebuffer : Framebuffer;
    public static screenplane : Mesh;

    private static COMPUTE_UBO_PNT : number;

    constructor(texSize : vec3, tex? : WebGLTexture, framebuffer? : Framebuffer) {
        super();
        this.mTexSize = texSize;
        this.mFramebuffer = framebuffer ? framebuffer : ResourceFactory.createFramebuffer(undefined, undefined, this.mTexSize.x, this.mTexSize.y);
        this.mTextureHandle = tex ? tex : framebuffer.color[0];
        this.mComputeIt = Math.ceil(this.mTexSize.z/WebGL2.MAX_DRAW_BUFFERS)
    }    

    public static _init() : void {
        ComputeWrapper.screenplane = MeshGenerator.getPlaneMesh();
        ComputeWrapper.COMPUTE_UBO_PNT = GLHelper.getNextBufferPnt();
    }

    // TODO: add clear option?!
    public dispatch(prog : WebGLProgram, from=new vec4([0,0,0,0]), to=new vec4([1,1,1,1])) {//, set_f : () => void = () => {}, reset_f : () => void = () => {}) {
        WebGL2.context.bindFramebuffer(WebGL2RenderingContext.FRAMEBUFFER, this.mFramebuffer.handle);
        let tmp = WebGL2.context.getParameter(WebGL2.context.VIEWPORT);
        WebGL2.context.viewport(0, 0, this.mTexSize.x, this.mTexSize.y);

        new UniformBuffer([
            new StructEntry("domain0", DataTypes.FLOAT32, 4, from.toArray()),
            new StructEntry("domain1", DataTypes.FLOAT32, 4, to.toArray()),
        ]).uploadBuffer(ComputeWrapper.COMPUTE_UBO_PNT);

        WebGL2.context.uniformBlockBinding(prog, WebGL2.context.getUniformBlockIndex(prog, "compute_prop"), ComputeWrapper.COMPUTE_UBO_PNT);
        
        if(this.mTexSize.z > 1) {
            let slice_pos = WebGL2.context.getUniformLocation(prog, "slice")
            for(var i = 0; i < this.mComputeIt; i++) {
                let dbuf : number[] = [];
                for(var j = 0; j < WebGL2.MAX_DRAW_BUFFERS; j++) {
                    if(i * WebGL2.MAX_DRAW_BUFFERS + j >= this.mTexSize.z) {
                        break;
                    }
                    dbuf.push(WebGL2RenderingContext.COLOR_ATTACHMENT0 + j);
                    WebGL2.context.framebufferTextureLayer(WebGL2RenderingContext.FRAMEBUFFER, WebGL2RenderingContext.COLOR_ATTACHMENT0 + j, this.mTextureHandle, 0, i * WebGL2.MAX_DRAW_BUFFERS + j);
                }

                //set_f();
                if(slice_pos) WebGL2.context.uniform1f(slice_pos, i);

                WebGL2.context.drawBuffers(dbuf);
                ComputeWrapper.screenplane.draw();
            }
        } else {
            let dbuf : number[] = [];
            for(var i = 0; i < this.mFramebuffer.color.length; i++) {
                dbuf.push(WebGL2RenderingContext.COLOR_ATTACHMENT0 + i);
            }

            //set_f();
            WebGL2.context.drawBuffers(dbuf);
            ComputeWrapper.screenplane.draw();
        }
        WebGL2.context.bindFramebuffer(WebGL2RenderingContext.FRAMEBUFFER, null);
        WebGL2.context.viewport(tmp[0], tmp[1], tmp[2], tmp[3]);

        //reset_f();
    }

    destroy() {}

}