import { Entity } from "../Entity";
import { WebGL2 } from "../../WebGL2";

export interface IComponent {
    setEntity(entity : Entity) : void;
    getEntity() : Entity;
}

export class Component implements IComponent {
    protected mEntity : Entity;

    setEntity(entity : Entity) : void {
        this.mEntity = entity;
    }
    getEntity() {
        return this.mEntity;
    }
}

export class WebGLComponent extends WebGL2 implements IComponent {
    protected mEntity : Entity;

    setEntity(entity : Entity) : void {
        this.mEntity = entity;
    }
    getEntity() {
        return this.mEntity;
    }
}