import { Grid } from "./Grid";
import { GridType } from "./GridBase";
import vec4 from "../utils/tsm/vec4";
import vec3 from "../utils/tsm/vec3";

export class Vec4Grid extends Grid<vec4> {
    constructor(size : vec3, sizeT = 1, boundary=new vec3([0,0,0])) {
        super(size, GridType.VEC4 | GridType.FLOAT, sizeT, boundary);
    }
}