import { TypedArray } from "./MathHelper";

export class DataType {
    private mByteSize : number;
    private mDataViewCreatorCallback : (ab : ArrayBuffer, off : number, size? : number) => TypedArray;
    constructor(byteSize : number, dataViewCreatorCallback : (ab : ArrayBuffer, off? : number, size? : number) => TypedArray) {
        this.mByteSize = byteSize;
        this.mDataViewCreatorCallback = dataViewCreatorCallback;
    }
    getByteSize () : number {
        return this.mByteSize;
    }
    getDataView(arrayBuffer : ArrayBuffer, off? : number, size? : number) : TypedArray {
        return this.mDataViewCreatorCallback(arrayBuffer, off, size);
    }
}

export class DataTypes {
    static readonly INT8 = new DataType(Int8Array.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Int8Array(ab, off, size));
    static readonly UINT8 = new DataType(Uint8Array.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Uint8Array(ab, off, size));
    static readonly INT16 = new DataType(Int16Array.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Int16Array(ab, off, size));
    static readonly UINT16 = new DataType(Uint16Array.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Uint16Array(ab, off, size));
    static readonly INT32 = new DataType(Int32Array.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Int32Array(ab, off, size));
    static readonly UINT32 = new DataType(Uint32Array.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Uint32Array(ab, off, size));
    static readonly UINT8_CLAMP = new DataType(Uint8ClampedArray.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Uint8ClampedArray(ab, off, size));
    static readonly FLOAT32 = new DataType(Float32Array.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Float32Array(ab, off, size));
    static readonly FLOAT64 = new DataType(Float64Array.BYTES_PER_ELEMENT, (ab : ArrayBuffer, off? : number, size? : number) => new Float64Array(ab.slice(off, off+size*8)));
}

export class StructEntry {
    name : string;
    type : DataType;
    size : number;
    def : number|number[]|TypedArray;
    constructor(name : string, type : DataType, size = 1, def?:number|number[]|TypedArray) {
        this.name = name;
        this.type = type;
        this.size = size;
        this.def = def;
    }
}

class DataIntervall {
    offset : number;
    length : number;
    dataType : DataType;
    constructor(start: number, length: number, dataType: DataType) {
        this.offset = start;
        this.length = length;
        this.dataType = dataType;
    }
}
export class StructBuffer {
    private mDataStructure = new Map<string, DataIntervall>();
    private mArrayBuffer : ArrayBuffer;

    constructor(dataStructure : StructEntry[]) {
        var offset = 0;
        for(let d of dataStructure) {
            this.mDataStructure.set(d.name, new DataIntervall(offset, d.size, d.type));
            offset += d.type.getByteSize() * d.size;
        }
        this.mArrayBuffer = new ArrayBuffer(offset);

        for(let d of dataStructure) {
            if(d.def){
                this.set(d.name, d.def);
            }
        }
    }
    set(name : string, val : number|number[]|TypedArray, off=0) {
        let entry = this.mDataStructure.get(name);
        let dataView = entry.dataType.getDataView(this.mArrayBuffer, entry.offset, entry.length);
        if(typeof val === "number"){
            entry.dataType.getDataView(this.mArrayBuffer, entry.offset, entry.length)[off] = val; 
        } else {
            for(var i = off; i < val.length; i++) {
                dataView[i] = val[i];
            } 
        }
    }

    get<T extends number|number[]|TypedArray>(name : string, off=0) : T {
        let entry = this.mDataStructure.get(name);
        let dataView = entry.dataType.getDataView(this.mArrayBuffer, entry.offset, entry.length);
        if(entry.length == 1) {
            return <T>dataView[off];
        }
        var val : number[] = [];
        for(var i = off; i < entry.length; i++) {
            val.push(dataView[i]);
        } 
        return <T>val;
    }

    setBuffer(arrayBuffer : ArrayBuffer) {
        this.mArrayBuffer = arrayBuffer;
    }

    getBuffer() {
        return this.mArrayBuffer;
    }
}