import { Resource } from './Resource'
import { GLHelper } from '../utils/GLHelper'
import { WebGL2 } from '../WebGL2';

export interface IVertexAttribInfo {
    handle: number;
    elem_size: number;
}

/**
 * Class which handles buffers of a mesh and provides different draw methods.
 */
export class Mesh extends Resource {
    static readonly POS_ATTR: IVertexAttribInfo = {
        handle: 0,
        elem_size: 3
    };
    static readonly TEX_COORD_ATTR: IVertexAttribInfo = {
        handle: 1,
        elem_size: 2
    };
    static readonly NOR_ATTR: IVertexAttribInfo = {
        handle: 2,
        elem_size: 3
    };
    static readonly TAN_ATTR: IVertexAttribInfo = {
        handle: 3,
        elem_size: 3
    };
    
    static readonly VERTEX_BYTE_STRIDE = (Mesh.POS_ATTR.elem_size + 
                                          Mesh.TEX_COORD_ATTR.elem_size + 
                                          Mesh.NOR_ATTR.elem_size + 
                                          Mesh.TAN_ATTR.elem_size) * 4;

    private readonly mVertexBuffer: WebGLBuffer;
    private readonly mIndexBuffer: WebGLBuffer;

    private readonly mIndexCount: number;

    /**
     * @param gl Context in which the resource will be constructed.
     * @param vertices Vertices of the mesh.
     * @param indices Indices of the mesh.
     */
    constructor(vertices: number[], indices: number[]) {
        super();
        this.mIndexCount = indices.length;

        this.mVertexBuffer = WebGL2.context.createBuffer()
        WebGL2.context.bindBuffer(WebGL2.context.ARRAY_BUFFER, this.mVertexBuffer);
        WebGL2.context.bufferData(WebGL2.context.ARRAY_BUFFER, new Float32Array(vertices), WebGL2.context.STATIC_DRAW);
        WebGL2.context.bindBuffer(WebGL2.context.ARRAY_BUFFER, null);

        this.mIndexBuffer = WebGL2.context.createBuffer();
        WebGL2.context.bindBuffer(WebGL2.context.ELEMENT_ARRAY_BUFFER, this.mIndexBuffer);
        WebGL2.context.bufferData(WebGL2.context.ELEMENT_ARRAY_BUFFER, new Int16Array(indices), WebGL2.context.STATIC_DRAW);
        WebGL2.context.bindBuffer(WebGL2.context.ELEMENT_ARRAY_BUFFER, null);

        GLHelper.CheckGLError();
    }

    /**
     * Draw the mesh.
     */
    draw() : void {
        WebGL2.context.bindBuffer(WebGL2.context.ARRAY_BUFFER, this.mVertexBuffer);

        WebGL2.context.enableVertexAttribArray(Mesh.POS_ATTR.handle);
        WebGL2.context.vertexAttribPointer(Mesh.POS_ATTR.handle, Mesh.POS_ATTR.elem_size, WebGL2.context.FLOAT, false, Mesh.VERTEX_BYTE_STRIDE, 0);

        WebGL2.context.enableVertexAttribArray(Mesh.TEX_COORD_ATTR.handle);
        WebGL2.context.vertexAttribPointer(Mesh.TEX_COORD_ATTR.handle, Mesh.TEX_COORD_ATTR.elem_size, WebGL2.context.FLOAT, false, Mesh.VERTEX_BYTE_STRIDE, Mesh.POS_ATTR.elem_size * 4);

        WebGL2.context.enableVertexAttribArray(Mesh.NOR_ATTR.handle);
        WebGL2.context.vertexAttribPointer(Mesh.NOR_ATTR.handle, Mesh.NOR_ATTR.elem_size, WebGL2.context.FLOAT, true, Mesh.VERTEX_BYTE_STRIDE, (Mesh.POS_ATTR.elem_size + Mesh.TEX_COORD_ATTR.elem_size) * 4);

        WebGL2.context.enableVertexAttribArray(Mesh.TAN_ATTR.handle);
        WebGL2.context.vertexAttribPointer(Mesh.TAN_ATTR.handle, Mesh.TAN_ATTR.elem_size, WebGL2.context.FLOAT, false, Mesh.VERTEX_BYTE_STRIDE, (Mesh.POS_ATTR.elem_size + Mesh.TEX_COORD_ATTR.elem_size + Mesh.NOR_ATTR.elem_size) * 4);

        WebGL2.context.bindBuffer(WebGL2.context.ELEMENT_ARRAY_BUFFER, this.mIndexBuffer);

        WebGL2.context.drawElements(WebGL2.context.TRIANGLES, this.mIndexCount, WebGL2.context.UNSIGNED_SHORT, 0);

        WebGL2.context.bindBuffer(WebGL2.context.ARRAY_BUFFER, null);
        WebGL2.context.bindBuffer(WebGL2.context.ELEMENT_ARRAY_BUFFER, null);

        GLHelper.CheckGLError();
    }

    /**
     * Draw multiple instances of the mesh.
     * @param count Number of instances.
     */
    drawInstanced(count: number) : void {
        WebGL2.context.bindBuffer(WebGL2.context.ARRAY_BUFFER, this.mVertexBuffer);

        WebGL2.context.enableVertexAttribArray(Mesh.POS_ATTR.handle);
        WebGL2.context.vertexAttribPointer(Mesh.POS_ATTR.handle, Mesh.POS_ATTR.elem_size, WebGL2.context.FLOAT, false, Mesh.VERTEX_BYTE_STRIDE, 0);

        WebGL2.context.enableVertexAttribArray(Mesh.TEX_COORD_ATTR.handle);
        WebGL2.context.vertexAttribPointer(Mesh.TEX_COORD_ATTR.handle, Mesh.TEX_COORD_ATTR.elem_size, WebGL2.context.FLOAT, false, Mesh.VERTEX_BYTE_STRIDE, Mesh.POS_ATTR.elem_size * 4);

        WebGL2.context.enableVertexAttribArray(Mesh.NOR_ATTR.handle);
        WebGL2.context.vertexAttribPointer(Mesh.NOR_ATTR.handle, Mesh.NOR_ATTR.elem_size, WebGL2.context.FLOAT, false, Mesh.VERTEX_BYTE_STRIDE, (Mesh.POS_ATTR.elem_size + Mesh.TEX_COORD_ATTR.elem_size) * 4);

        WebGL2.context.enableVertexAttribArray(Mesh.TAN_ATTR.handle);
        WebGL2.context.vertexAttribPointer(Mesh.TAN_ATTR.handle, Mesh.TAN_ATTR.elem_size, WebGL2.context.FLOAT, false, Mesh.VERTEX_BYTE_STRIDE, (Mesh.POS_ATTR.elem_size + Mesh.TEX_COORD_ATTR.elem_size + Mesh.NOR_ATTR.elem_size) * 4);
        
        WebGL2.context.bindBuffer(WebGL2.context.ELEMENT_ARRAY_BUFFER, this.mIndexBuffer);

        WebGL2.context.drawElementsInstanced(WebGL2.context.TRIANGLES, this.mIndexCount, WebGL2.context.UNSIGNED_SHORT, 0, count);

        WebGL2.context.bindBuffer(WebGL2.context.ARRAY_BUFFER, null);
        WebGL2.context.bindBuffer(WebGL2.context.ELEMENT_ARRAY_BUFFER, null);

        GLHelper.CheckGLError();
    }

    /**
     * Destroy buffers of the mesh.
     */
    destroy() : void {
        WebGL2.context.deleteBuffer(this.mVertexBuffer);
        WebGL2.context.deleteBuffer(this.mIndexBuffer);
    }
}
