import { RenderComponent } from "./RenderComponent";
import { FloatGrid } from "../../resources/FloatGrid";
import { Mesh } from "../../resources/Mesh";
import { MeshGenerator } from "../../utils/MeshGenerator";
import * as vsSource from '../../shaders/raytracer_vs.glsl'
import * as fsSource from '../../shaders/raytracer_fs.glsl'
import { WebGL2 } from "../../WebGL2";
import { RenderEngine } from "../../renderers/RenderEngine"
import { UniformBuffer } from "../../resources/UniformBuffer";
import { StructEntry, DataTypes } from "../../utils/StructBuffer";
import { TypedArray } from "../../utils/MathHelper";
import { ResourceFactory } from "../../utils/ResourceFactory";
import { ResourceLocation } from "../../utils/ResourceLocation";
import vec4 from "../../utils/tsm/vec4";

export class VolumeRenderComponent extends RenderComponent {
    private static mRaytracerProgram : WebGLProgram;
    //private static mGradientProgram : WebGLProgram;
    //private static mNormalAndCurvatureProgram : WebGLProgram;

    private static mDomain : Mesh;

    private static M_MATRIX_LOC : WebGLUniformLocation;
    private static INV_M_MATRIX_LOC : WebGLUniformLocation;

    private static PHI_TEX_LOC : ResourceLocation<WebGLUniformLocation>;
    //private static NOR_CURV_LOC : ResourceLocation<WebGLUniformLocation>;
    //private static GRADIENT_LOC : ResourceLocation<WebGLUniformLocation>;
    
    private static MAT_UBO_LOC : ResourceLocation<number>;
    private static PROP_UBO_LOC : ResourceLocation<number>;

    private static CAM_UBO_LOC : number;
    private static SKY_TEX_LOC : WebGLUniformLocation;

    private mMaterial : UniformBuffer;

    private mVolumeTexture : FloatGrid;
    
    //private mGradient : WebGLTexture;
    //private mNormalAndCurvature : WebGLTexture;

    constructor(volumeTexture : FloatGrid) {
        super();

        this.mVolumeTexture = volumeTexture;

        this.mMaterial = new UniformBuffer([
            new StructEntry("reflCol", DataTypes.FLOAT32, 4, [1,1,1,1]),
            new StructEntry("diffCol", DataTypes.FLOAT32, 4, [0,0,0,0]),
            new StructEntry("specCol", DataTypes.FLOAT32, 4, [1,1,1,1]),
            new StructEntry("volCol", DataTypes.FLOAT32, 4, [0,0,0,0]),
            new StructEntry("shinniness", DataTypes.INT32, 1, 32),
            new StructEntry("dens_fac", DataTypes.FLOAT32, 1, 1),
            new StructEntry("_pad", DataTypes.INT32, 2)
        ]);
    }

    public static _init() : void {
        VolumeRenderComponent.mRaytracerProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2.context.VERTEX_SHADER, code: vsSource.default },
            { type: WebGL2.context.FRAGMENT_SHADER, code: fsSource.default }
        ]);
        VolumeRenderComponent.M_MATRIX_LOC = WebGL2.context.getUniformLocation(VolumeRenderComponent.mRaytracerProgram, "model");
        VolumeRenderComponent.INV_M_MATRIX_LOC = WebGL2.context.getUniformLocation(VolumeRenderComponent.mRaytracerProgram, "inv_m");
        VolumeRenderComponent.CAM_UBO_LOC = WebGL2.context.getUniformBlockIndex(VolumeRenderComponent.mRaytracerProgram, "cam_data");
        VolumeRenderComponent.MAT_UBO_LOC = new ResourceLocation(WebGL2.context.getUniformBlockIndex(VolumeRenderComponent.mRaytracerProgram, "material"));
        VolumeRenderComponent.PROP_UBO_LOC = new ResourceLocation(WebGL2.context.getUniformBlockIndex(VolumeRenderComponent.mRaytracerProgram, "properties"));
        VolumeRenderComponent.SKY_TEX_LOC = WebGL2.context.getUniformLocation(VolumeRenderComponent.mRaytracerProgram, "sky");
        VolumeRenderComponent.PHI_TEX_LOC = new ResourceLocation(WebGL2.context.getUniformLocation(VolumeRenderComponent.mRaytracerProgram, "phi"));

        VolumeRenderComponent.mDomain = MeshGenerator.getCubeMesh();
    }

    setVolumeTexture(volumeTexture: FloatGrid) {
        this.mVolumeTexture = volumeTexture;
    }
    getVolumeTexture() : FloatGrid {
        return this.mVolumeTexture;
    }

    setMaterialEntry(name : string, val : number|number[]|TypedArray){
        this.mMaterial.set(name, val);
    }
    
    draw() : void {
        this.mVolumeTexture.getPropBuffer().uploadBuffer(VolumeRenderComponent.PROP_UBO_LOC.pnt);
        this.mMaterial.uploadBuffer(VolumeRenderComponent.MAT_UBO_LOC.pnt);

        WebGL2.context.useProgram(VolumeRenderComponent.mRaytracerProgram);

        WebGL2.context.uniformBlockBinding(VolumeRenderComponent.mRaytracerProgram, VolumeRenderComponent.MAT_UBO_LOC.loc, VolumeRenderComponent.MAT_UBO_LOC.pnt);
        WebGL2.context.uniformBlockBinding(VolumeRenderComponent.mRaytracerProgram, VolumeRenderComponent.PROP_UBO_LOC.loc, VolumeRenderComponent.PROP_UBO_LOC.pnt);
        WebGL2.context.uniformBlockBinding(VolumeRenderComponent.mRaytracerProgram, VolumeRenderComponent.CAM_UBO_LOC, RenderEngine.CAM_BUFFER_PNT);

        WebGL2.context.uniformMatrix4fv(VolumeRenderComponent.M_MATRIX_LOC, false, this.mEntity.getTransform().getLocalToWorld().toArray());
        WebGL2.context.uniformMatrix4fv(VolumeRenderComponent.INV_M_MATRIX_LOC, false, this.mEntity.getTransform().getWorldToLocal().toArray());
        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + VolumeRenderComponent.PHI_TEX_LOC.pnt);
        this.mVolumeTexture.bindTexture();

        WebGL2.context.uniform1i(VolumeRenderComponent.PHI_TEX_LOC.loc, VolumeRenderComponent.PHI_TEX_LOC.pnt);
        WebGL2.context.uniform1i(VolumeRenderComponent.SKY_TEX_LOC, RenderEngine.SKY_TEX_PNT);

        VolumeRenderComponent.mDomain.draw();

        this.mVolumeTexture.unbindTexture();
    }
}