import { Resource } from "./Resource";
import { WebGL2 } from "../WebGL2";
import { GLHelper } from "../utils/GLHelper";

export class ParticleSystem extends Resource {
    private mPositionBuffer : WebGLBuffer;
    private mParticleCount : number;

    private mParticleScale : number;

    constructor(scale = 1) {
        super();
        this.mParticleScale = scale;
        this.mPositionBuffer = WebGL2.context.createBuffer();
    }

    setScale(scale : number) {
        this.mParticleScale = this.mParticleScale;
    }

    load(buf : Float32Array) {
        this.mParticleCount = buf.length/3;
        
        WebGL2.context.bindBuffer(WebGL2RenderingContext.ARRAY_BUFFER, this.mPositionBuffer);
        WebGL2.context.bufferData(WebGL2RenderingContext.ARRAY_BUFFER, buf, WebGL2.context.STATIC_DRAW);
        WebGL2.context.bindBuffer(WebGL2RenderingContext.ARRAY_BUFFER, null);

        GLHelper.CheckGLError();
    }

    count() : number {
        return this.mParticleCount;
    }

    scale() : number {
        return this.mParticleScale;
    }

    bind() {
        WebGL2.context.bindBuffer(WebGL2RenderingContext.ARRAY_BUFFER, this.mPositionBuffer);
    }

    destroy(): void {
        throw new Error("Method not implemented.");
    }
}