import { Transform } from "./components/Transform"
import { RenderComponent } from "./components/RenderComponent";
import { Scene } from "./Scene";
import { LightComponent } from "./components/LightComponent";
import { CameraComponent } from "./components/CameraComponent";

export class Entity {
    private mScene : Scene;
    private mTransform : Transform;
    
    private mRenderComponent : RenderComponent;
    private mLightComponent : LightComponent;
    private mCameraComponent : CameraComponent;
    
    constructor(scene : Scene) {
        this.mTransform = new Transform();
        this.mTransform.setEntity(this);
        this.mScene = scene;
    }

    getScene() {
        return this.mScene;
    }

    setCameraComponent(component : CameraComponent){
        this.mCameraComponent = component;
        this.mCameraComponent.setEntity(this);
        this.mScene.setCamera(component);
    }
    getCameraComponent<T extends CameraComponent>() : T {
        return <T>this.mCameraComponent;
    }
    removeCameraComponent() {
        if (this.mCameraComponent) {
            if (this.mScene.mCamera === this.mCameraComponent) {
                this.mScene.setCamera(null);
            }
            this.mCameraComponent = null;
        }
    }
    setRenderComponent(component : RenderComponent){
        if (this.mRenderComponent) {
            this.mScene.removeRenderComponent(this.mRenderComponent);
        }
        this.mRenderComponent = component;
        this.mRenderComponent.setEntity(this);
        this.mScene.addRenderComponent(component);
    }
    getRenderComponent<T extends RenderComponent>() : T {
        return <T>this.mRenderComponent;
    }
    removeRenderComponent() {
        if (this.mRenderComponent) {
            this.mScene.removeRenderComponent(this.mRenderComponent);
            this.mRenderComponent = null;
        }
    }
    setLightComponent(component : LightComponent){
        if (this.mLightComponent) {
            this.mScene.removeLightComponent(this.mLightComponent);
        }
        this.mLightComponent = component;
        this.mLightComponent.setEntity(this);
        this.mScene.addLightComponent(component);
    }
    getLightComponent<T extends LightComponent>() : T {
        return <T>this.mLightComponent;
    }
    removeLightComponent() {
        if (this.mLightComponent) {
            this.mScene.removeLightComponent(this.mLightComponent);
            this.mLightComponent = null;
        }
    }

    getTransform() : Transform {
        return this.mTransform;
    }

    destroy() {
        if (this.mRenderComponent) {
            this.mScene.removeRenderComponent(this.mRenderComponent);
        }
        if (this.mLightComponent) {
            this.mScene.removeLightComponent(this.mLightComponent);
        }
        if (this.mCameraComponent) {
            this.mScene.setCamera(null);
        }
    }
}