import { RenderComponent } from "./RenderComponent";
import { Mesh } from "../../resources/Mesh";
import { UniformBuffer } from "../../resources/UniformBuffer";
import { ResourceLocation } from "../../utils/ResourceLocation";
import * as vsSolid from '../../shaders/solid_vs.glsl'
import * as fsSolid from '../../shaders/solid_fs.glsl'
import { ResourceFactory } from "../../utils/ResourceFactory";
import { WebGL2 } from "../../WebGL2";
import { TypedArray } from "../../utils/MathHelper";
import { StructEntry, DataTypes } from "../../utils/StructBuffer";
import { RenderEngine } from "../../renderers/RenderEngine";
import vec4 from "../../utils/tsm/vec4";

export class SolidRenderComponent extends RenderComponent {
    private static mSolidProgram : WebGLProgram;

    private static M_MATRIX_LOC : WebGLUniformLocation;
    private static INV_M_MATRIX_LOC : WebGLUniformLocation;
    private static MAT_UBO_LOC : ResourceLocation<number>;
    private static TEX_LOC : ResourceLocation<WebGLUniformLocation>;
    private static NOR_LOC : ResourceLocation<WebGLUniformLocation>;

    private static CAM_UBO_LOC : number;
    private static SKY_TEX_LOC : WebGLUniformLocation;

    private mMesh : Mesh;

    private mMaterial : UniformBuffer;
    private mTexture : WebGLTexture;
    private mNormal : WebGLTexture;
    
    constructor(mesh : Mesh) {
        super();

        this.mMesh = mesh;

        this.mMaterial = new UniformBuffer([
            new StructEntry("reflCol", DataTypes.FLOAT32, 4, [0,0,0,1.0]),
            new StructEntry("diffCol", DataTypes.FLOAT32, 4, [0.5,0.5,0.5,1]),
            new StructEntry("specCol", DataTypes.FLOAT32, 4, [0.5,0.5,0.5,1]),
            new StructEntry("shinniness", DataTypes.INT32, 1, 16),
            new StructEntry("_pad", DataTypes.INT32, 3)
        ]);

        this.mTexture = ResourceFactory.createTextureWithConstCol(new vec4([1.0, 0.0, 0.4, 1.0]));
        this.mNormal = ResourceFactory.createTextureWithConstCol(new vec4([0.5, 0.5, 1.0, 1.0]));
    }

    public static _init() : void {
        SolidRenderComponent.mSolidProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2.context.VERTEX_SHADER, code: vsSolid.default },
            { type: WebGL2.context.FRAGMENT_SHADER, code: fsSolid.default }
        ]);

        SolidRenderComponent.M_MATRIX_LOC = WebGL2.context.getUniformLocation(SolidRenderComponent.mSolidProgram, "model");
        SolidRenderComponent.INV_M_MATRIX_LOC = WebGL2.context.getUniformLocation(SolidRenderComponent.mSolidProgram, "inv_m");
        SolidRenderComponent.CAM_UBO_LOC = WebGL2.context.getUniformBlockIndex(SolidRenderComponent.mSolidProgram, "cam_data");
        SolidRenderComponent.MAT_UBO_LOC = new ResourceLocation(WebGL2.context.getUniformBlockIndex(SolidRenderComponent.mSolidProgram, "material"));
        SolidRenderComponent.SKY_TEX_LOC = WebGL2.context.getUniformLocation(SolidRenderComponent.mSolidProgram, "sky");

        SolidRenderComponent.TEX_LOC = new ResourceLocation(WebGL2.context.getUniformLocation(SolidRenderComponent.mSolidProgram, "tex"));
        SolidRenderComponent.NOR_LOC = new ResourceLocation(WebGL2.context.getUniformLocation(SolidRenderComponent.mSolidProgram, "normal_tex"));
    }

    setTexture(tex : WebGLTexture){
        this.mTexture = tex;
    }

    setNormal(tex : WebGLTexture){
        this.mNormal = tex;
    }

    setMaterialEntry(name : string, val : number|number[]|TypedArray){
        this.mMaterial.set(name, val);
    }
    
    draw() : void {
        WebGL2.context.useProgram(SolidRenderComponent.mSolidProgram);
        this.mMaterial.uploadBuffer(SolidRenderComponent.MAT_UBO_LOC.pnt);

        WebGL2.context.uniformBlockBinding(SolidRenderComponent.mSolidProgram, SolidRenderComponent.MAT_UBO_LOC.loc, SolidRenderComponent.MAT_UBO_LOC.pnt);
        WebGL2.context.uniformBlockBinding(SolidRenderComponent.mSolidProgram, SolidRenderComponent.CAM_UBO_LOC, RenderEngine.CAM_BUFFER_PNT);

        WebGL2.context.uniformMatrix4fv(SolidRenderComponent.M_MATRIX_LOC, false, this.mEntity.getTransform().getLocalToWorld().toArray());
        WebGL2.context.uniformMatrix4fv(SolidRenderComponent.INV_M_MATRIX_LOC, false, this.mEntity.getTransform().getWorldToLocal().toArray());
        
        WebGL2.context.uniform1i(SolidRenderComponent.SKY_TEX_LOC, RenderEngine.SKY_TEX_PNT);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + SolidRenderComponent.TEX_LOC.pnt);
        WebGL2.context.bindTexture(WebGL2.context.TEXTURE_2D, this.mTexture);
        WebGL2.context.uniform1i(SolidRenderComponent.TEX_LOC.loc, SolidRenderComponent.TEX_LOC.pnt);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + SolidRenderComponent.NOR_LOC.pnt);
        WebGL2.context.bindTexture(WebGL2.context.TEXTURE_2D, this.mNormal);
        WebGL2.context.uniform1i(SolidRenderComponent.NOR_LOC.loc, SolidRenderComponent.NOR_LOC.pnt);

        this.mMesh.draw();
    }
}