import { Resource } from "./Resource";
import { GLHelper } from "../utils/GLHelper";
import { WebGL2 } from "../WebGL2";
import { Grid } from "./Grid"
import { TypedArray } from "../utils/MathHelper";
import { ResourceFactory } from "../utils/ResourceFactory";
import * as screenVS from '../shaders/screen_vs.glsl'
import * as advectFS from '../shaders/advect_fs.glsl'
import * as sliceFS from '../shaders/slice_fs.glsl'
import * as constFS from '../shaders/const_fs.glsl'
import * as addFS from '../shaders/bi_add_fs.glsl'
import * as mulFS from '../shaders/bi_mul_fs.glsl'
import { ComputeWrapper } from "./ComputeWrapper";
import { UniformBuffer } from "./UniformBuffer";
import { StructEntry, DataTypes } from "../utils/StructBuffer";
import vec4 from "../utils/tsm/vec4";
import vec3 from "../utils/tsm/vec3";
import vec2 from "../utils/tsm/vec2";

export enum GridType {
    FLOAT = 1,
    INT = 2,
    VEC3 = 4,
    VEC4 = 8
}

export type ElementType = number|vec2|vec3|vec4;
export type VecType = vec2|vec3|vec4;

export function iterateGrid(func : (cell : vec4, idx : number) => void, size : vec3, off = new vec3([0, 0, 0]), sizeT = 1, offT = 0) {
    for(var t = offT; t < sizeT; t++){
        for(var z = off.z; z < size.z; z++){
            for(var y = off.y; y < size.y; y++){
                for(var x = off.x; x < size.x; x++){
                    func(new vec4([x,y,z,t]), t * size.x * size.y * size.z + z * size.x * size.y + y * size.x + x);
                }
            }
        }
    }
}

// TODO: support I16/F16?!!
export class GridBase extends Resource {
    protected mSize: vec3;
    protected mSizeT : number;
    protected mTexSize: vec3;
    protected mArraySize : vec3;
    protected mDim : number;
    protected mBoundary : vec3;
    protected mBoundaryVal = 0;
    protected mType : GridType;

    private mTextureHandle : WebGLTexture;
    private mTextureType : number;
    private mTextureFormat : number;

    protected mComputeShader : ComputeWrapper;

    private static mAdvectProgram : WebGLShader;
    private static mSliceProgram : WebGLShader;
    private static mConstProgram : WebGLShader;
    private static mAddProgram : WebGLShader;
    private static mMulProgram : WebGLShader;

    private static PROP0_UBO_PNT : number;
    private static PROP1_UBO_PNT : number;

    private static CONST_F_UBO_PNT : number;

    private static G0_TEX_PNT : number;
    private static G1_TEX_PNT : number;
    
    private mPropBuffer : UniformBuffer;

    constructor(size : vec3, type : GridType, sizeT = 1, boundary=new vec3([0,0,0]), boundaryVal=0) {
        super();
        this.mSize = size.copy();
        this.mSizeT = sizeT;
        this.mTexSize = size.copy();
        this.mArraySize = new vec3([1, 1, 1]);
        this.mDim = sizeT > 1 ? 4 : (size.z > 1) ? 3 : 2;
        this.mType = type;
        this.mBoundary = boundary.copy();
        this.mBoundaryVal = boundaryVal;
        
        if(this.is4D()) {
            let maxArraySize = vec3.floor(vec3.quotient(new vec3([WebGL2.MAX_3D_TEX_SIZE, WebGL2.MAX_3D_TEX_SIZE, WebGL2.MAX_3D_TEX_SIZE]), this.mSize));

            let reqArray = vec3.ceil(vec3.quotient(new vec3([this.mSizeT, this.mSizeT, this.mSizeT]), new vec3([1, maxArraySize.x, maxArraySize.x * maxArraySize.y])));

            this.mArraySize = vec3.min(maxArraySize, reqArray);
            this.mTexSize = vec3.product(this.mArraySize, this.mSize);
        } 

        if(this.is2D()) {
            if(this.mTexSize.x > WebGL2.MAX_2D_TEX_SIZE || this.mTexSize.y > WebGL2.MAX_2D_TEX_SIZE) {
                console.error("2D Grid (" + this.mTexSize.x + "x" + this.mTexSize.y + ") too big! Max size: " + WebGL2.MAX_2D_TEX_SIZE + "x" + WebGL2.MAX_2D_TEX_SIZE);
                return;
            }
        } else {
            if(this.mTexSize.x > WebGL2.MAX_3D_TEX_SIZE || this.mTexSize.y > WebGL2.MAX_3D_TEX_SIZE ||this.mTexSize.z > WebGL2.MAX_3D_TEX_SIZE) {
                console.error("3D Grid (" + this.mTexSize.x + "x" + this.mTexSize.y + "x" + this.mTexSize.z + ") too big! Max size: " + WebGL2.MAX_3D_TEX_SIZE + "x" + WebGL2.MAX_3D_TEX_SIZE + "x" + WebGL2.MAX_2D_TEX_SIZE);
                return;
            }
        }

        if(this.containsVec()) {
            this.mTextureFormat = this.containsFloat() ?  WebGL2RenderingContext.RGBA32F : WebGL2RenderingContext.RGBA32I;
        } else {
            this.mTextureFormat = this.containsFloat() ?  WebGL2RenderingContext.R32F : WebGL2RenderingContext.R32I;
        }
        this.mTextureHandle = WebGL2.context.createTexture();
        this.mTextureType = this.is2D() ? WebGL2RenderingContext.TEXTURE_2D : WebGL2RenderingContext.TEXTURE_3D;

        this.bindTexture();
        WebGL2.context.texParameteri(this.mTextureType, WebGL2.context.TEXTURE_MIN_FILTER, WebGL2.context.LINEAR);
        WebGL2.context.texParameteri(this.mTextureType, WebGL2.context.TEXTURE_MAG_FILTER, WebGL2.context.LINEAR);
        WebGL2.context.texParameteri(this.mTextureType, WebGL2.context.TEXTURE_WRAP_R, WebGL2.context.CLAMP_TO_EDGE);
        WebGL2.context.texParameteri(this.mTextureType, WebGL2.context.TEXTURE_WRAP_S, WebGL2.context.CLAMP_TO_EDGE);
        WebGL2.context.texParameteri(this.mTextureType, WebGL2.context.TEXTURE_WRAP_T, WebGL2.context.CLAMP_TO_EDGE);

        if(this.is2D()) {
            WebGL2.context.texStorage2D(WebGL2RenderingContext.TEXTURE_2D, 1, this.mTextureFormat, this.mTexSize.x, this.mTexSize.y);
        } else {
            WebGL2.context.texStorage3D(WebGL2RenderingContext.TEXTURE_3D, 1, this.mTextureFormat, this.mTexSize.x, this.mTexSize.y, this.mTexSize.z);
        }

        //this.fillGrid(1);

        this.unbindTexture();

        this.mPropBuffer = new UniformBuffer([
            new StructEntry("offset", DataTypes.FLOAT32, 4, [0,0,0]),
            new StructEntry("nextOffset", DataTypes.FLOAT32, 3, [0,0,0]),
            new StructEntry("frac", DataTypes.FLOAT32, 1, 0),
            new StructEntry("downScale", DataTypes.FLOAT32, 4, [1,1,1]),
            new StructEntry("boundary", DataTypes.FLOAT32, 3, vec3.quotient(this.mBoundary, this.mSize).toArray()),
            new StructEntry("boundaryVal", DataTypes.FLOAT32, 1, this.mBoundaryVal),
            new StructEntry("resolution", DataTypes.FLOAT32, 3, vec3.quotient(new vec3([1,1,1]), this.mSize).toArray()),
            new StructEntry("timesteps", DataTypes.FLOAT32, 1, this.mSizeT)
        ]);

        this.setTimeSlice(0);

        this.mComputeShader = ResourceFactory.createComputeWrapperFromTexture(this.mTextureHandle, this.mTexSize);

        GLHelper.CheckGLError();

        if(this.mTextureHandle === 0) {
            console.error("No texture created!");
        }
    }

    static _init() : void {
        GridBase.mAdvectProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2RenderingContext.VERTEX_SHADER, code: screenVS.default },
            { type: WebGL2RenderingContext.FRAGMENT_SHADER, code: advectFS.default }
        ]);
        GridBase.mSliceProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2RenderingContext.VERTEX_SHADER, code: screenVS.default },
            { type: WebGL2RenderingContext.FRAGMENT_SHADER, code: sliceFS.default }
        ]);
        GridBase.mConstProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2RenderingContext.VERTEX_SHADER, code: screenVS.default },
            { type: WebGL2RenderingContext.FRAGMENT_SHADER, code: constFS.default }
        ]);
        GridBase.mAddProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2RenderingContext.VERTEX_SHADER, code: screenVS.default },
            { type: WebGL2RenderingContext.FRAGMENT_SHADER, code: addFS.default }
        ]);
        GridBase.mMulProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2RenderingContext.VERTEX_SHADER, code: screenVS.default },
            { type: WebGL2RenderingContext.FRAGMENT_SHADER, code: mulFS.default }
        ]);
        GridBase.PROP0_UBO_PNT = GLHelper.getNextBufferPnt();
        GridBase.PROP1_UBO_PNT = GLHelper.getNextBufferPnt();

        GridBase.CONST_F_UBO_PNT = GLHelper.getNextBufferPnt();

        GridBase.G0_TEX_PNT = GLHelper.getNextTexPnt();
        GridBase.G1_TEX_PNT = GLHelper.getNextTexPnt();
    }

    reloadBoundary() {
        this.fillGrid(this.mBoundaryVal, new vec3([0, 0, 0]), new vec3([this.mBoundary.x, this.mSize.y, this.mSize.z]));
        this.fillGrid(this.mBoundaryVal, new vec3([0, 0, 0]), new vec3([this.mSize.x, this.mBoundary.y, this.mSize.z]));
        this.fillGrid(this.mBoundaryVal, new vec3([0, 0, 0]), new vec3([this.mSize.x, this.mSize.y, this.mBoundary.z]));

        this.fillGrid(this.mBoundaryVal, new vec3([this.mSize.x-this.mBoundary.x, 0, 0]), new vec3([this.mSize.x, this.mSize.y, this.mSize.z]));
        this.fillGrid(this.mBoundaryVal, new vec3([0, this.mSize.y-this.mBoundary.y, 0]), new vec3([this.mSize.x, this.mSize.y, this.mSize.z]));
        this.fillGrid(this.mBoundaryVal, new vec3([0, 0, this.mSize.z-this.mBoundary.z]), new vec3([this.mSize.x, this.mSize.y, this.mSize.z]));
    }

    inBounds(idx : vec3) {
        return  idx.x >= this.mBoundary.x && idx.x < this.mSize.x - this.mBoundary.x &&
                idx.y >= this.mBoundary.y && idx.y < this.mSize.y - this.mBoundary.y &&
                idx.z >= this.mBoundary.z && idx.z < this.mSize.z - this.mBoundary.z;
    }

    loadTexture(buf : TypedArray, from = new vec3([0, 0, 0]), to = this.mTexSize) {        
        this.bindTexture();
        if(this.is2D()) {
            WebGL2.context.texSubImage2D(WebGL2RenderingContext.TEXTURE_2D, 0, 
                from.x, from.y, (to.x - from.x), (to.y - from.y), 
                this.containsVec() ? WebGL2RenderingContext.RGBA : WebGL2RenderingContext.RED, 
                this.containsFloat() ? WebGL2RenderingContext.FLOAT : WebGL2RenderingContext.INT, buf);
        } else {
            WebGL2.context.texSubImage3D(WebGL2RenderingContext.TEXTURE_3D, 0,
                from.x, from.y, from.z, (to.x - from.x), (to.y - from.y), (to.z - from.z), 
                this.containsVec() ? WebGL2RenderingContext.RGBA : WebGL2RenderingContext.RED, 
                this.containsFloat() ? WebGL2RenderingContext.FLOAT : WebGL2RenderingContext.INT, buf);
        } 
        this.unbindTexture();

        GLHelper.CheckGLError();
    }

    load(buf : TypedArray, from = new vec3([0, 0, 0]), to = this.mSize, fromT = 0, toT = this.mSizeT) {
        if(this.is4D()) {
            let stride = (to.x - from.x) * (to.y - from.y) * (to.z - from.z) * this.getElementCnt();
            for(var i = fromT; i < toT; i++) {
                var idx = this.getArrayIndex(i);
                this.loadTexture(buf.slice(i * stride, (i+1) * stride), vec3.sum(idx, from), vec3.sum(idx, to));
            }
        } else {
            this.loadTexture(buf, from, to);
        }
        if(this.mBoundary.x > 0 && this.mBoundary.y > 0 && this.mBoundary.z > 0 && buf[0] != this.mBoundaryVal){
            this.reloadBoundary();
        }
    }

    fillGrid(x : ElementType, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        let s = (to.x - from.x) * (to.y - from.y) * (to.z - from.z) * (toT - fromT);
        let buf = new ArrayBuffer(s * this.getBytesPerElement());
        let fbuf = this.containsFloat() ? new Float32Array(buf) : new Int32Array(buf);
        if(typeof x === "number") {
            fbuf.fill(<number>x);
        } else {
            let x_buf = x.toArray();
            for(var i = 0; i < fbuf.length; i++) {
                fbuf[i] = x_buf[i%x_buf.length];
            }
        }
        this.load(fbuf, from, to, fromT, toT);
    }

    private constFunc(result? : GridBase, addVal = 0.0, mulVal = 1.0, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        var copy = false;
        if(!result) {
            result = ResourceFactory.createGridFromOther(this);
            copy = true;
        }

        this.mPropBuffer.uploadBuffer(GridBase.PROP0_UBO_PNT);

        new UniformBuffer([
            new StructEntry("add_cnst", DataTypes.FLOAT32, 1, addVal),
            new StructEntry("mul_cnst", DataTypes.FLOAT32, 1, mulVal),
            new StructEntry("_pad", DataTypes.INT32, 2)
        ]).uploadBuffer(GridBase.CONST_F_UBO_PNT);
        
        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + GridBase.G0_TEX_PNT);
        this.bindTexture();

        WebGL2.context.useProgram(GridBase.mConstProgram);

        WebGL2.context.uniformBlockBinding(GridBase.mConstProgram, WebGL2.context.getUniformBlockIndex(GridBase.mConstProgram, "properties"), GridBase.PROP0_UBO_PNT);
        WebGL2.context.uniformBlockBinding(GridBase.mConstProgram, WebGL2.context.getUniformBlockIndex(GridBase.mConstProgram, "const_func"), GridBase.CONST_F_UBO_PNT);

        WebGL2.context.uniform1i(WebGL2.context.getUniformLocation(GridBase.mConstProgram, "g"), GridBase.G0_TEX_PNT);

        result.mComputeShader.dispatch(
            GridBase.mConstProgram, 
            new vec4([from.x/this.mSize.x, from.y/this.mSize.y, from.z/this.mSize.z, fromT]),
            new vec4([to.x/this.mSize.x, to.y/this.mSize.y, to.z/this.mSize.z, toT]));

        if(copy) this.copy(result);
    }

    multConst(val : number, result? : GridBase, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        this.constFunc(result, 0.0, val, from, to, fromT, toT);
    }

    addConst(val : number, result? : GridBase, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        this.constFunc(result, val, 1.0, from, to, fromT, toT);
    }

    setConst(val : number, result? : GridBase, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        this.constFunc(result, val, 0.0, from, to, fromT, toT);
    }

    copy(other : GridBase, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        other.constFunc(this, 0.0, 1.0, from, to, fromT, toT);
    }

    multiply(other : GridBase, result? : GridBase, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        var copy = false;
        if(!result) {
            result = ResourceFactory.createGridFromOther(this);
            copy = true;
        }
        this.mPropBuffer.uploadBuffer(GridBase.PROP0_UBO_PNT);
        other.mPropBuffer.uploadBuffer(GridBase.PROP1_UBO_PNT);

        WebGL2.context.useProgram(GridBase.mMulProgram);

        WebGL2.context.uniformBlockBinding(GridBase.mMulProgram, WebGL2.context.getUniformBlockIndex(GridBase.mMulProgram, "properties0"), GridBase.PROP0_UBO_PNT);
        WebGL2.context.uniformBlockBinding(GridBase.mMulProgram, WebGL2.context.getUniformBlockIndex(GridBase.mMulProgram, "properties1"), GridBase.PROP1_UBO_PNT);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + GridBase.G0_TEX_PNT);
        this.bindTexture();
        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + GridBase.G1_TEX_PNT);
        other.bindTexture();

        WebGL2.context.uniform1i(WebGL2.context.getUniformLocation(GridBase.mMulProgram, "g0"), GridBase.G0_TEX_PNT);
        WebGL2.context.uniform1i(WebGL2.context.getUniformLocation(GridBase.mMulProgram, "g1"), GridBase.G1_TEX_PNT);

        result.mComputeShader.dispatch(
            GridBase.mMulProgram, 
            new vec4([from.x/this.mSize.x, from.y/this.mSize.y, from.z/this.mSize.z, fromT]),
            new vec4([to.x/this.mSize.x, to.y/this.mSize.y, to.z/this.mSize.z, toT]));

        if(copy) this.copy(result);
    }

    add(other : GridBase, result? : GridBase, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        var copy = false;
        if(!result) {
            result = ResourceFactory.createGridFromOther(this);
            copy = true;
        }
        this.mPropBuffer.uploadBuffer(GridBase.PROP0_UBO_PNT);
        other.mPropBuffer.uploadBuffer(GridBase.PROP1_UBO_PNT);

        WebGL2.context.useProgram(GridBase.mAddProgram);

        WebGL2.context.uniformBlockBinding(GridBase.mAddProgram, WebGL2.context.getUniformBlockIndex(GridBase.mAddProgram, "properties0"), GridBase.PROP0_UBO_PNT);
        WebGL2.context.uniformBlockBinding(GridBase.mAddProgram, WebGL2.context.getUniformBlockIndex(GridBase.mAddProgram, "properties1"), GridBase.PROP1_UBO_PNT);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + GridBase.G0_TEX_PNT);
        this.bindTexture();
        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + GridBase.G1_TEX_PNT);
        other.bindTexture();

        WebGL2.context.uniform1i(WebGL2.context.getUniformLocation(GridBase.mAddProgram, "g0"), GridBase.G0_TEX_PNT);
        WebGL2.context.uniform1i(WebGL2.context.getUniformLocation(GridBase.mAddProgram, "g1"), GridBase.G1_TEX_PNT);

        result.mComputeShader.dispatch(
            GridBase.mAddProgram, 
            new vec4([from.x/this.mSize.x, from.y/this.mSize.y, from.z/this.mSize.z, fromT]),
            new vec4([to.x/this.mSize.x, to.y/this.mSize.y, to.z/this.mSize.z, toT]));

        if(copy) this.copy(result);
    }

    slice(result : GridBase, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        this.mPropBuffer.uploadBuffer(GridBase.PROP0_UBO_PNT);

        WebGL2.context.useProgram(GridBase.mSliceProgram);

        WebGL2.context.uniformBlockBinding(GridBase.mSliceProgram, WebGL2.context.getUniformBlockIndex(GridBase.mSliceProgram, "properties"), GridBase.PROP0_UBO_PNT);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + GridBase.G0_TEX_PNT);
        this.bindTexture();

        WebGL2.context.uniform1i(WebGL2.context.getUniformLocation(GridBase.mSliceProgram, "g"), GridBase.G0_TEX_PNT);

        result.mComputeShader.dispatch(
            GridBase.mSliceProgram, 
            new vec4([from.x/this.mSize.x, from.y/this.mSize.y, from.z/this.mSize.z, fromT]),
            new vec4([to.x/this.mSize.x, to.y/this.mSize.y, to.z/this.mSize.z, toT]));
    }

    advect(vel : Grid<VecType>, result? : GridBase, from = this.mBoundary, to = vec3.difference(this.mSize, this.mBoundary), fromT = 0, toT = this.mSizeT) {
        var copy = false;
        if(!result) {
            result = ResourceFactory.createGridFromOther(this);
            copy = true;
        }
        this.mPropBuffer.uploadBuffer(GridBase.PROP0_UBO_PNT);
        vel.mPropBuffer.uploadBuffer(GridBase.PROP1_UBO_PNT);

        WebGL2.context.useProgram(GridBase.mAdvectProgram);

        WebGL2.context.uniformBlockBinding(GridBase.mAdvectProgram, WebGL2.context.getUniformBlockIndex(GridBase.mAdvectProgram, "properties0"), GridBase.PROP0_UBO_PNT);
        WebGL2.context.uniformBlockBinding(GridBase.mAdvectProgram, WebGL2.context.getUniformBlockIndex(GridBase.mAdvectProgram, "properties1"), GridBase.PROP1_UBO_PNT);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + GridBase.G0_TEX_PNT);
        this.bindTexture();
        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + GridBase.G1_TEX_PNT);
        vel.bindTexture();

        WebGL2.context.uniform1i(WebGL2.context.getUniformLocation(GridBase.mAdvectProgram, "g0"), GridBase.G0_TEX_PNT);
        WebGL2.context.uniform1i(WebGL2.context.getUniformLocation(GridBase.mAdvectProgram, "g1"), GridBase.G1_TEX_PNT);

        result.mComputeShader.dispatch(
            GridBase.mAdvectProgram, 
            new vec4([from.x/this.mSize.x, from.y/this.mSize.y, from.z/this.mSize.z, fromT]),
            new vec4([to.x/this.mSize.x, to.y/this.mSize.y, to.z/this.mSize.z, toT]));

        if(copy) this.copy(result);
    }

    is2D() {
        return this.mDim === 2;
    }

    is3D() {
        return this.mDim === 3;
    }

    is4D() {
        return this.mDim === 4;
    }

    containsVec() {
        return this.mType & (GridType.VEC3|GridType.VEC4);
    }

    containsFloat() {
        return this.mType & GridType.FLOAT;
    }

    getType() : GridType {
        return this.mType;
    }

    getSize() : vec3 {
        return this.mSize;
    }

    getSizeT() : number {
        return this.mSizeT;
    }

    getTexSize() : vec3 {
        return this.mTexSize;
    }

    getArraySize() : vec3 {
        return this.mArraySize;
    }

    getSizeFlat() : number {
        var size = this.mSizeT;
        for(let s of this.mSize.toArray()) {
            size *= s;
        }
        return size;
    }

    getElementCnt() : number {
        return this.containsVec() ? 4 : 1;
    }

    getBytesPerElement() : number {
        return 4 * this.getElementCnt();
    }

    getByteSizeFlat() : number {
        return this.getSizeFlat() * this.getBytesPerElement();
    }

    getArrayIndex(t : number) : vec3 {
        let idx = new vec3();
        idx.x = Math.floor(t % this.mArraySize.x) * this.mSize.x;
        idx.y = (Math.floor(t / this.mArraySize.x) % this.mArraySize.y) * this.mSize.y;
        idx.z = (Math.floor(t / (this.mArraySize.x * this.mArraySize.y)) % this.mArraySize.z) * this.mSize.z;
        return idx;
    }

    getArrayPos(t : number) : vec3 {
        return vec3.quotient(this.getArrayIndex(t), this.mTexSize);
    }

    setTimeSlice(t : number) {
        /*if(t < 0) {
            this.setProperty("offset", new vec3(0,0,0));
            this.setProperty("nextOffset", new vec3(0,0,0));
            this.setProperty("frac", 0);
            this.setProperty("downScale", new vec3(1,1,1));
            this.setProperty("resolution", new vec3(1.0/this.mTexSize.x, 1.0/this.mTexSize.y, 1.0/this.mTexSize.z))
        } else {*/
        this.setProperty("offset", this.getArrayPos(t).toArray());
        this.setProperty("nextOffset", this.getArrayPos(t+1).toArray());
        this.setProperty("frac", t - Math.floor(t));
        this.setProperty("downScale", vec3.quotient(new vec3([1,1,1]), this.mArraySize).toArray());
        //}
    }

    protected setProperty(name : string, val : number|number[]|TypedArray){
        this.mPropBuffer.set(name, val);
    }

    getPropBuffer() : UniformBuffer {
        return this.mPropBuffer;
    }

    getTextureFormat() : number {
        return this.mTextureFormat;
    }

    getTextureHandle() : WebGLTexture {
        return this.mTextureHandle;
    }

    getBoundary() : vec3 {
        return this.mBoundary;
    }

    getBoundaryVal() : number {
        return this.mBoundaryVal;
    }

    setBoundary(boundary : vec3) : void {
        this.mBoundary = boundary;   
        this.setProperty("boundary", vec3.quotient(this.mBoundary, this.mSize).toArray());
    }

    setBoundaryVal(val : number) {
        this.mBoundaryVal;
        this.setProperty("boundaryVal", val);
    }

    bindTexture() : void {
        WebGL2.context.bindTexture(this.mTextureType, this.mTextureHandle);
    }
    
    unbindTexture() : void {
        WebGL2.context.bindTexture(this.mTextureType, null);
    }

    destroy() : void {
        WebGL2.context.deleteTexture(this.mTextureHandle);
        WebGL2.context.deleteBuffer(this.mPropBuffer);
    }
} 