import { VolumeRenderComponent } from "../scene/components/VolumeRenderComponent";
import { GLHelper } from "../utils/GLHelper";
import { WebGL2 } from "../WebGL2";
import { Scene } from "../scene/Scene";
import * as screenVS from '../shaders/screen_vs.glsl'
import * as mergerFS from '../shaders/merger_fs.glsl'
import * as skyVS from '../shaders/sky_vs.glsl'
import * as skyFS from '../shaders/sky_fs.glsl'
import { RenderComponent } from "../scene/components/RenderComponent";
import { UniformBuffer } from "../resources/UniformBuffer";
import { StructEntry, DataTypes } from "../utils/StructBuffer";
import { CameraComponent } from "../scene/components/CameraComponent";
import { ResourceFactory } from "../utils/ResourceFactory";
import { Mesh } from "../resources/Mesh";
import { MeshGenerator } from "../utils/MeshGenerator";
import { ResourceLocation } from "../utils/ResourceLocation";
import { SolidRenderComponent } from "../scene/components/SolidRenderComponent";
import { Framebuffer } from "../resources/Framebuffer";
import { ComputeWrapper } from "../resources/ComputeWrapper";
import { ParticleRenderComponent } from "../scene/components/ParticleRenderComponent";

export class RenderEngine extends WebGL2 {
    public static readonly CAM_BUFFER_PNT = GLHelper.getNextBufferPnt();
    public static readonly SKY_TEX_PNT = GLHelper.getNextTexPnt();

    private static readonly MAX_LIGHT_CNT = 16;

    protected mCamera : CameraComponent;
    protected mScene : Scene;

    private mCameraBuffer : UniformBuffer;

    private mSkyTexture : WebGLTexture;

    private static mMergerFramebuffer : Framebuffer;

    private static mMergerProgram : WebGLShader;
    private static SOLID_COL_LOC : ResourceLocation<WebGLUniformLocation>;
    private static VOL_COL_LOC : ResourceLocation<WebGLUniformLocation>;
    private static VOL_DISP_LOC : ResourceLocation<WebGLUniformLocation>;
    private static VOL_DEPTH_LOC : ResourceLocation<WebGLUniformLocation>;

    private static mSkyProgram : WebGLShader;
    private static mSkybox : Mesh;
    private static CAM_UBO_LOC : number;

    constructor(camera : CameraComponent, skyTexture : WebGLTexture){
        super();

        this.mCamera = camera;
        WebGL2.context.clearColor(0.0, 0.0, 0.0, 0.0);
        WebGL2.context.enable(WebGL2.context.CULL_FACE);
        WebGL2.context.enable(WebGL2.context.DEPTH_TEST);

        this.mSkyTexture = skyTexture;

        this.mCameraBuffer = new UniformBuffer([
            new StructEntry("view", DataTypes.FLOAT32, 16),
            new StructEntry("proj", DataTypes.FLOAT32, 16),
            new StructEntry("pos", DataTypes.FLOAT32, 4),
            new StructEntry("lights", DataTypes.FLOAT32, 8 * RenderEngine.MAX_LIGHT_CNT),
            new StructEntry("lightCount", DataTypes.INT32, 1),
            new StructEntry("ambientFac", DataTypes.FLOAT32, 1),
            new StructEntry("_pad", DataTypes.INT32, 2)
        ]);
    }

    static _init() {
        RenderEngine.mMergerProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2RenderingContext.VERTEX_SHADER, code: screenVS.default },
            { type: WebGL2RenderingContext.FRAGMENT_SHADER, code: mergerFS.default }
        ]);
        RenderEngine.SOLID_COL_LOC = new ResourceLocation(WebGL2.context.getUniformLocation(RenderEngine.mMergerProgram, "solidColor"));
        RenderEngine.VOL_COL_LOC = new ResourceLocation(WebGL2.context.getUniformLocation(RenderEngine.mMergerProgram, "volumeColor"));
        RenderEngine.VOL_DISP_LOC = new ResourceLocation(WebGL2.context.getUniformLocation(RenderEngine.mMergerProgram, "volumeDisplace"));
        RenderEngine.VOL_DEPTH_LOC = new ResourceLocation(WebGL2.context.getUniformLocation(RenderEngine.mMergerProgram, "volumeDepth"));

        RenderEngine.mSkyProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2RenderingContext.VERTEX_SHADER, code: skyVS.default },
            { type: WebGL2RenderingContext.FRAGMENT_SHADER, code: skyFS.default }
        ]);
        RenderEngine.mSkybox = MeshGenerator.getCubeMesh();
        RenderEngine.CAM_UBO_LOC = WebGL2.context.getUniformBlockIndex(RenderEngine.mSkyProgram, "cam_data");

        RenderEngine.mMergerFramebuffer = ResourceFactory.createFramebuffer([WebGL2RenderingContext.RGBA16F, WebGL2RenderingContext.RGBA16F, WebGL2RenderingContext.RGBA16F], WebGL2RenderingContext.DEPTH_COMPONENT24);

        VolumeRenderComponent._init();
        SolidRenderComponent._init();
        ParticleRenderComponent._init();
        ComputeWrapper._init();
    }

    setSkyTexture() {
        //TODO
    }

    setScene(scene : Scene) {
        this.mScene = scene;
    }

    resize() : void {
        WebGL2.context.viewport(0, 0, WebGL2.context.drawingBufferWidth, WebGL2.context.drawingBufferHeight);
    }

    private updateCamBuffer() : void {
        this.mCameraBuffer.set("view", this.mCamera.getEntity().getTransform().getWorldToLocal().toArray());
        this.mCameraBuffer.set("proj", this.mCamera.getProjMatrix().toArray());
        this.mCameraBuffer.set("pos", this.mCamera.getEntity().getTransform().getPostion().toArray());
        this.mCameraBuffer.set("pos", 1, 3);

        let lights = this.mScene.getLightComponents();
        let fb = new Float32Array(8 * lights.length);

        for(var i = 0; i < lights.length; i++){
            fb.set(lights[i].getEntity().getTransform().getPostion().toArray(), i*8);
            fb.set([1], i*8+3);
            fb.set(lights[i].getColor().toArray(), i*8+4);
            fb.set([1], i*8+7);
        }
        this.mCameraBuffer.set("ambientFac", 0.0);
        this.mCameraBuffer.set("lights", fb);
        this.mCameraBuffer.set("lightCount", lights.length);

        this.mCameraBuffer.uploadBuffer(RenderEngine.CAM_BUFFER_PNT);
    }

    private drawAll(rc : RenderComponent[]) : void {
        if(rc) {
            for(const r of rc) {        
                r.draw();
            }
        }
    }

    draw() : void {
        WebGL2.context.clear(WebGL2.context.DEPTH_BUFFER_BIT | WebGL2.context.COLOR_BUFFER_BIT);
        WebGL2.context.depthFunc(WebGL2RenderingContext.LESS);
        WebGL2.context.enable(WebGL2RenderingContext.CULL_FACE);

        this.updateCamBuffer();

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + RenderEngine.SKY_TEX_PNT);
        WebGL2.context.bindTexture(WebGL2RenderingContext.TEXTURE_CUBE_MAP, this.mSkyTexture);
        

        WebGL2.context.bindFramebuffer(WebGL2RenderingContext.FRAMEBUFFER, RenderEngine.mMergerFramebuffer.handle);
        WebGL2.context.drawBuffers([WebGL2RenderingContext.NONE, WebGL2RenderingContext.COLOR_ATTACHMENT1, WebGL2RenderingContext.COLOR_ATTACHMENT2]);

        WebGL2.context.clear(WebGL2RenderingContext.DEPTH_BUFFER_BIT | WebGL2RenderingContext.COLOR_BUFFER_BIT);

        this.drawAll(this.mScene.getRenderComponents(VolumeRenderComponent.name));

        WebGL2.context.depthFunc(WebGL2RenderingContext.GREATER);
        WebGL2.context.depthMask(false);

        WebGL2.context.drawBuffers([WebGL2RenderingContext.COLOR_ATTACHMENT0]);
        WebGL2.context.clear(WebGL2RenderingContext.COLOR_BUFFER_BIT);
        this.drawAll(this.mScene.getRenderComponents(SolidRenderComponent.name));
        this.drawAll(this.mScene.getRenderComponents(ParticleRenderComponent.name));

        WebGL2.context.bindFramebuffer(WebGL2RenderingContext.FRAMEBUFFER, null);
        
        
        WebGL2.context.depthFunc(WebGL2RenderingContext.LESS);
        WebGL2.context.depthMask(true);

        WebGL2.context.useProgram(RenderEngine.mMergerProgram);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + RenderEngine.SOLID_COL_LOC.pnt);
        WebGL2.context.bindTexture(WebGL2RenderingContext.TEXTURE_2D, RenderEngine.mMergerFramebuffer.color[0]);
        WebGL2.context.uniform1i(RenderEngine.SOLID_COL_LOC.loc, RenderEngine.SOLID_COL_LOC.pnt);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + RenderEngine.VOL_COL_LOC.pnt);
        WebGL2.context.bindTexture(WebGL2RenderingContext.TEXTURE_2D, RenderEngine.mMergerFramebuffer.color[1]);
        WebGL2.context.uniform1i(RenderEngine.VOL_COL_LOC.loc, RenderEngine.VOL_COL_LOC.pnt);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + RenderEngine.VOL_DISP_LOC.pnt);
        WebGL2.context.bindTexture(WebGL2RenderingContext.TEXTURE_2D, RenderEngine.mMergerFramebuffer.color[2]);
        WebGL2.context.uniform1i(RenderEngine.VOL_DISP_LOC.loc, RenderEngine.VOL_DISP_LOC.pnt);

        WebGL2.context.activeTexture(WebGL2RenderingContext.TEXTURE0 + RenderEngine.VOL_DEPTH_LOC.pnt);
        WebGL2.context.bindTexture(WebGL2RenderingContext.TEXTURE_2D, RenderEngine.mMergerFramebuffer.depth);
        WebGL2.context.uniform1i(RenderEngine.VOL_DEPTH_LOC.loc, RenderEngine.VOL_DEPTH_LOC.pnt);

        ComputeWrapper.screenplane.draw();

        this.drawAll(this.mScene.getRenderComponents(SolidRenderComponent.name));
        this.drawAll(this.mScene.getRenderComponents(ParticleRenderComponent.name));

        WebGL2.context.depthFunc(WebGL2RenderingContext.LEQUAL);
        WebGL2.context.disable(WebGL2RenderingContext.CULL_FACE);

        WebGL2.context.useProgram(RenderEngine.mSkyProgram);
        WebGL2.context.uniformBlockBinding(RenderEngine.mSkyProgram, RenderEngine.CAM_UBO_LOC, RenderEngine.CAM_BUFFER_PNT);

        RenderEngine.mSkybox.draw();
    }
}