import { MathHelper } from '../../utils/MathHelper';
import { Component } from './Component';
import vec3 from '../../utils/tsm/vec3';
import quat from '../../utils/tsm/quat';
import mat4 from '../../utils/tsm/mat4';

export class Transform extends Component {
    static readonly FWD     = new vec3([0, 0, -1]);
    static readonly UP      = new vec3([0, 1, 0]);
    static readonly RIGHT   = new vec3([1, 0, 0]);

    private mPosition       : vec3;
    private mRotation       : quat;
    private mScale          : vec3;
    
    private mForward        : vec3;
    private mUp             : vec3;
    private mRight          : vec3;

    private mLocalToWorld   : mat4;
    private mWorldToLocal   : mat4;

    constructor() {
        super();
        this.mPosition       = vec3.zero.copy();
        this.mRotation       = quat.identity.copy();
        this.mScale          = vec3.one.copy();

        this.mForward        = Transform.FWD;
        this.mUp             = Transform.UP;
        this.mRight          = Transform.RIGHT;
    
        this.mLocalToWorld   = mat4.identity.copy();
        this.mWorldToLocal   = mat4.identity.copy();
    }

    getForward() : vec3 {
        return this.mForward;
    }
    getUp() : vec3 {
        return this.mUp;
    }
    getRight() : vec3 {
        return this.mRight;
    }

    getLocalToWorld() : mat4 {
        return this.mLocalToWorld;
    }

    getWorldToLocal() : mat4 {
        return this.mWorldToLocal;
    }

    getPostion() : vec3 {
        return this.mPosition;
    }

    getScale() : vec3 {
        return this.mScale;
    }

    getRotation() : quat {
        return this.mRotation;
    }

    setPosition(pos : vec3) : void {
        pos.copy(this.mPosition);
        this.updateMatrices();
    }

    setRotation(rot : quat) : void {
        rot.copy(this.mRotation);
        this.updateMatrices();
    }

    setScale(scale : vec3) : void {
        scale.copy(this.mScale);
        this.updateMatrices();
    }

    setRotationEuler(rot : vec3) : void {
        rot.toQuat(this.mRotation);
        this.updateMatrices();
    }

    lookAt(target : vec3) : void {
        let dir = vec3.difference(target, this.mPosition).normalize(); 

        let rot1 = quat.rotationTo(this.mForward, dir);

        let right = vec3.cross(dir, Transform.UP);
        let up = vec3.cross(right, dir).normalize();
        
        dir = rot1.multiplyVec3(this.mUp);
        let rot2 = quat.rotationTo(dir, up);

        this.mRotation = quat.product(rot2, rot1);
        this.updateMatrices();
    }

    private updateMatrices() : void {
        mat4.fromRotationTranslationScale(this.mRotation, this.mPosition, this.mScale, this.mLocalToWorld);
        this.mWorldToLocal.setIdentity();
        this.mWorldToLocal.scale(this.mScale.copy().inverse());
        this.mWorldToLocal.multiply(this.mRotation.copy().inverse().toMat4());
        this.mWorldToLocal.translate(this.mPosition.copy().negate());
    }
}