import { PhysicEngine } from '../physics/PhysicEngine'
import { RenderComponent } from './components/RenderComponent';
import { Entity } from './Entity';
import { CameraComponent } from './components/CameraComponent';
import { LightComponent } from './components/LightComponent';

/**
 * Abstract class of a scene which handles the construction and behaviour of differnt objects the virtual world.
 */
export abstract class Scene {
    mCamera : CameraComponent;
    mPhysicEngine: PhysicEngine;

    private mRenderComponents = new Map<string, RenderComponent[]>();
    private mLightComponents : LightComponent[] = [];

    constructor() {}

    /**
     * Set camera which renders scene.
     * @param camera Camera object.
     */
    setCamera(camera : CameraComponent) : void {
        this.mCamera = camera;
    }

    /**
     * Set physic engine which determine the behaviour of the objects in the scene.
     * @param physicEngine PhysicEngine object.
     */
    setPhysicEngine(physicEngine: PhysicEngine) : void {
        this.mPhysicEngine = physicEngine;
    }

    addRenderComponent(rc : RenderComponent) : void {
        if(!this.mRenderComponents.has(rc.constructor.name)) {
            this.mRenderComponents.set(rc.constructor.name, [rc]);
        } else {
            this.mRenderComponents.get(rc.constructor.name).push(rc);
        }
    }

    addLightComponent(lc : LightComponent) : void {
        this.mLightComponents.push(lc);
    }

    removeRenderComponent(rc : RenderComponent) : void {
        if (this.mRenderComponents.has(rc.constructor.name)) {
            let rcs = this.mRenderComponents.get(rc.constructor.name);
            let idx = rcs.indexOf(rc, 0);
            if (idx > -1) {
                rcs.splice(idx, 1);
                this.mRenderComponents.set(rc.constructor.name, rcs);
            }
        } 
    }

    removeLightComponent(lc : LightComponent) : void {
        let idx = this.mLightComponents.indexOf(lc, 0);
        if (idx > -1) {
            this.mLightComponents.splice(idx, 1);
        }
    }

    getRenderComponents<T extends RenderComponent>(type : string) : T[] {
        return <T[]>this.mRenderComponents.get(type);
    }

    getLightComponents() : LightComponent[] {
        return this.mLightComponents;
    }

    createEntity() : Entity {
        return new Entity(this);
    }

    removeEntity(entity : Entity) {
        entity.destroy();
    }

    /**
     * Create and initialize objects of the scene.
     */
    abstract init() : void;

    /**
     * Update objects in the scene.
     * @param dt Delta time since the last frame.
     */
    abstract update(dt: number) : void;
    render(){ this.mCamera.render(); }
}