import { RenderEngine } from "../../renderers/RenderEngine";
import { WebGLComponent } from "./Component";
import { Entity } from "../Entity";
import mat4 from "../../utils/tsm/mat4";

export class CameraComponent extends WebGLComponent {
    protected mRenderer : RenderEngine;
    
    protected mProjMatrix : mat4;

    protected mFovy : number;
    protected mNear : number;
    protected mFar : number;

    constructor(skyTexture : WebGLTexture, fovy=45, near=1, far=100) {
        super();
        this.mFovy = fovy * Math.PI / 180;
        this.mNear = near;
        this.mFar = far;
        this.mRenderer = new RenderEngine(this, skyTexture);
        this.resize();
    }

    setEntity(entity : Entity) : void{
        super.setEntity(entity);
        this.mRenderer.setScene(this.mEntity.getScene());
    }

    getProjMatrix() : mat4 {
        return this.mProjMatrix;
    }

    resize() {
        const w = WebGLComponent.canvas.clientWidth;
        const h = WebGLComponent.canvas.clientHeight;
        this.mProjMatrix = mat4.perspective(this.mFovy, w/h, this.mNear, this.mFar);
        this.mRenderer.resize()
    }

    render() : void {
        this.mRenderer.draw();
    }
}