    private gridIdx(x : number, y : number, z : number) : number {
        return x + this.gridSize.x * (y + z * this.gridSize.y);
    }

    public set(pos : vec3, radius : number = 0, normal? : vec3) {
        const start = vec3.floor(vec3.difference(pos, new vec3([radius+0.5, radius+0.5, radius+0.5])));
        const end = vec3.ceil(vec3.sum(pos, new vec3([radius-0.5, radius-0.5, radius-0.5])));

        vec3.clamp(start, vec3.zero, vec3.difference(this.gridSize, vec3.one), start);
        vec3.clamp(end, vec3.zero, vec3.difference(this.gridSize, vec3.one), end);
        
        for(var z = start.z; z <= end.z; z++){
            for(var y = start.y; y <= end.y; y++) {
                for(var x = start.x; x <= end.x; x++) {
                    const p = new vec3([x+0.5, y+0.5, z+0.5]);
                    const dist = vec3.distance(p, pos);
                    if(dist < Math.abs(this.triangleIdx[this.gridIdx(x, y, z)])) {
                        const sign = normal == undefined ? 1 : Math.sign(vec3.dot(vec3.difference(p, pos), normal));
                        this.triangleIdx[this.gridIdx(x, y, z)] = sign * vec3.distance(p, pos);
                    }
                }
            }
        }
    }

