import { StructBuffer, StructEntry } from "../utils/StructBuffer";
import { WebGL2 } from "../WebGL2";
import { Resource } from "./Resource";
import { GLHelper } from "../utils/GLHelper";
import { TypedArray } from "../utils/MathHelper";

export class UniformBuffer extends Resource {
    private mHandle : WebGLBuffer;
    private mStructBuffer : StructBuffer;
    constructor(dataStructure : StructEntry[]) {
        super();
        this.mStructBuffer = new StructBuffer(dataStructure);
        this.mHandle = WebGL2.context.createBuffer();
    }

    set(name : string, val : number|number[]|TypedArray, off=0) {
        this.mStructBuffer.set(name, val, off);
    }

    get(name : string, off=0) {
        return this.mStructBuffer.get(name, off);
    }

    setBuffer(arrayBuffer : ArrayBuffer) {
        this.mStructBuffer.setBuffer(arrayBuffer);
    }

    getBuffer() {
        return this.mStructBuffer.getBuffer();
    }

    uploadBuffer(pnt : number) {
        WebGL2.context.bindBuffer(WebGL2RenderingContext.UNIFORM_BUFFER, this.mHandle);
        WebGL2.context.bindBufferBase(WebGL2RenderingContext.UNIFORM_BUFFER, pnt, this.mHandle);
        WebGL2.context.bufferData(WebGL2RenderingContext.UNIFORM_BUFFER, this.mStructBuffer.getBuffer(), WebGL2RenderingContext.DYNAMIC_DRAW);
        WebGL2.context.bindBuffer(WebGL2RenderingContext.UNIFORM_BUFFER, null);
        GLHelper.CheckGLError();
    }

    destroy() {
        WebGL2.context.deleteBuffer(this.mHandle);        
    }
}