import { Component } from "./Component";
import { Entity } from "../Entity";
import vec3 from "../../utils/tsm/vec3";

export class LightComponent extends Component {
    private mColor : vec3;
    constructor(color = new vec3([1,1,1])) {
        super();
        this.mColor = color;
    }
    setColor(color : vec3){
        this.mColor = color;
    }
    getColor() {
        return this.mColor;
    }
}