import { GridBase, GridType, ElementType } from "./GridBase";
import vec3 from "../utils/tsm/vec3";
//import { WebGL2 } from "../WebGL2";

export class Grid<T extends ElementType> extends GridBase {
    constructor(size : vec3, type : GridType, sizeT = 1, boundary=new vec3([0, 0, 0]), boundaryVal=0) {
        super(size, type, sizeT, boundary, boundaryVal);
    }

    fillGrid(x : T, off = new vec3([0, 0, 0]), size = this.mSize, offT = 0, sizeT = this.mSizeT) {
        super.fillGrid(x, off, size, offT, sizeT);
    }

    destroy() : void {
        super.destroy();
    }
}