import { WebGL2 } from "../WebGL2";

/**
 * Different static GL helper functions.
 */
export class GLHelper extends WebGL2 {
    /**
     * Maps the GL error enum to a more verbose output.
     */
    static readonly glErrorMap = new Map([
        [ WebGL2RenderingContext.NO_ERROR, "NO_ERROR: No error has been recorded." ],
        [ WebGL2RenderingContext.INVALID_ENUM, "INVALID_ENUM: An unacceptable value has been specified for an enumerated argument. The command is ignored and the error flag is set." ],
        [ WebGL2RenderingContext.INVALID_VALUE, "INVALID_VALUE: A numeric argument is out of range. The command is ignored and the error flag is set." ],
        [ WebGL2RenderingContext.INVALID_OPERATION, "INVALID_OPERATION: The specified command is not allowed for the current state. The command is ignored and the error flag is set." ],
        [ WebGL2RenderingContext.OUT_OF_MEMORY, "OUT_OF_MEMORY: Not enough memory is left to execute the command." ],
        [ WebGL2RenderingContext.INVALID_FRAMEBUFFER_OPERATION, "INVALID_FRAMEBUFFER_OPERATION: The currently bound framebuffer is not framebuffer complete when trying to render to or to read from it." ],
        [ WebGL2RenderingContext.CONTEXT_LOST_WEBGL, "CONTEXT_LOST_WEBGL: If the WebGL context is lost. Until the context is restored, it returns gl.NO_ERROR."]
    ]);

    private static BUFFER_PNT_CNT = 0;
    private static TEX_PNT_CNT = 0;

    static getNextBufferPnt() {
        return ++this.BUFFER_PNT_CNT;
    }
    static getNextTexPnt() {
        return ++this.TEX_PNT_CNT;
    }

    /*static readonly glTypeMap = new Map([
        [ WebGL2RenderingContext.R16F, 16 ],
        [ WebGL2RenderingContext.R32F, 32 ]
    ]);

    static getByteSize(type : number) : number {
        
    }*/

    /**
     * Checks the GL error status and throws an exception if an error occured.
     * @param gl WebGL context.
     */
    static CheckGLError() : void {
        const err = WebGL2.context.getError();
        if (err != WebGL2.context.NO_ERROR) {
            throw new Error(GLHelper.glErrorMap.get(err));
        }
    }
    
    static finish() {
        WebGL2.context.finish();
    }
}