export class UniHeader {
    type : string;
    size : vec3;
    elementType : number;
    bytesPerElement : number;
    info : string;
    timestamp : number;

    copy(other: UniHeader) {
        other.type = this.type;
        other.size = this.size;
        other.elementType = this.elementType;
        other.bytesPerElement = this.bytesPerElement;
        other.info = this.info;
        other.timestamp = this.timestamp;
    }
}

export class UniGridHeader extends UniHeader {
    gridType : number;
    sizeT : number;

    copy(other: UniGridHeader) {
        super.copy(other);
        other.gridType = this.gridType;
        other.sizeT = this.sizeT;
    }
}

export class UniPartHeader extends UniHeader {
    static readonly BYTE_SIZE = 292;

    count : number;

    private mStructBuffer = new StructBuffer([
        new StructEntry("type", DataTypes.UINT8, 4),
        new StructEntry("count", DataTypes.INT32),
        new StructEntry("size", DataTypes.INT32, 3),
        new StructEntry("elementType", DataTypes.INT32),
        new StructEntry("bytesPerElement", DataTypes.INT32),
        new StructEntry("info", DataTypes.UINT8, 256),
        new StructEntry("timestamp", DataTypes.FLOAT64)
    ]);

    constructor(ab : ArrayBuffer) {
        super();
        this.mStructBuffer.setBuffer(ab);
        this.type = String.fromCharCode.apply(null, this.mStructBuffer.get("type"));
        this.count = this.mStructBuffer.get("count");
        this.size = new vec3(this.mStructBuffer.get("size"));
        this.elementType = this.mStructBuffer.get("elementType");
        this.bytesPerElement = this.mStructBuffer.get("bytesPerElement");
        this.info = String.fromCharCode.apply(null, this.mStructBuffer.get("info"));
        this.timestamp = this.mStructBuffer.get("timestamp");
    }

    copy(other: UniPartHeader) {
        super.copy(other);
        other.count = this.count;
    }
}

class UniHeaderLegacyWrapper extends UniGridHeader {
    static readonly BYTE_SIZE = 292;

    private mStructBuffer = new StructBuffer([
        new StructEntry("type", DataTypes.UINT8, 4),
        new StructEntry("size", DataTypes.INT32, 3),
        new StructEntry("gridType", DataTypes.INT32),
        new StructEntry("elementType", DataTypes.INT32),
        new StructEntry("bytesPerElement", DataTypes.INT32),
        new StructEntry("info", DataTypes.UINT8, 256),
        new StructEntry("timestamp", DataTypes.FLOAT64),
        new StructEntry("sizeT", DataTypes.INT32)
    ]);

    constructor(ab : ArrayBuffer) {
        super();
        this.mStructBuffer.setBuffer(ab);
        this.type = String.fromCharCode.apply(null, this.mStructBuffer.get("type"));
        this.size = new vec3(this.mStructBuffer.get("size"));
        this.gridType = this.mStructBuffer.get("gridType");
        this.elementType = this.mStructBuffer.get("elementType");
        this.bytesPerElement = this.mStructBuffer.get("bytesPerElement");
        this.info = String.fromCharCode.apply(null, this.mStructBuffer.get("info"));
        this.timestamp = this.mStructBuffer.get("timestamp");
        if(ab.byteLength > UniHeaderLegacyWrapper.BYTE_SIZE) this.sizeT = this.mStructBuffer.get("sizeT");
    }
}

