    static getUniHeader(data : ArrayBuffer) : UniHeader {
        let data_type = ResourceFactory.getUniFileType(data);
        if(data_type !== "PB02" && data_type !== "M4T3" && data_type !== "M4T2" && data_type !== "MNT3" && data_type !== "MNT2") {
            console.error("'" + data_type + "' format not supported, have to be PB02, M4T3, M4T2, MNT3 or MNT2!");
            return null;
        }
        if (data_type === "MNT2") {
            let header = new UniHeaderLegacyWrapper(data.slice(0, UniHeaderLegacyWrapper.BYTE_SIZE));
            header.sizeT = 1;
            return header;
        } else if (data_type == "M4T2") {
            return new UniHeaderLegacyWrapper(data.slice(0, UniHeaderLegacyWrapper.BYTE_SIZE + 4));
        } else if (data_type == "M4T3" || data_type == "MNT3") {
            let header = new UniHeaderWrapper(data.slice(0, UniHeaderWrapper.BYTE_SIZE));
            if(header.sizeT === 0) {
                header.sizeT = 1;
            }
            return header;
        } else {
            return new UniPartHeader(data.slice(0, UniPartHeader.BYTE_SIZE));
        }
    }

    static getUniFileType(data : ArrayBuffer) {
        return String.fromCharCode.apply(null, Array.from(new Uint8Array(data, 0, 4)));
    }

    static createGridFromUni<T extends GridBase>(data : ArrayBuffer, boundary=vec3.zero.copy(), headerOut: UniGridHeader = undefined) : T {
        let tex : GridBase;        
        let data_type = ResourceFactory.getUniFileType(data);
        if(data_type !== "M4T3" && data_type !== "M4T2" && data_type !== "MNT3" && data_type !== "MNT2") {
            console.error("'" + data_type + "' format not supported, have to be M4T3, M4T2, MNT3 or MNT2!");
            return null;
        }

        let header : UniHeaderLegacyWrapper | UniHeaderWrapper;
        let byteSize : number;
        if (data_type === "MNT2") {
            header = new UniHeaderLegacyWrapper(data.slice(0, UniHeaderLegacyWrapper.BYTE_SIZE));
            header.sizeT = 1;
            byteSize = UniHeaderLegacyWrapper.BYTE_SIZE;
        } else if (data_type == "M4T2") {
            header = new UniHeaderLegacyWrapper(data.slice(0, UniHeaderLegacyWrapper.BYTE_SIZE + 4));
            byteSize = UniHeaderLegacyWrapper.BYTE_SIZE + 4;
        } else {
            header = new UniHeaderWrapper(data.slice(0, UniHeaderWrapper.BYTE_SIZE));
            if(header.sizeT === 0) {
                header.sizeT = 1;
            }
            byteSize = UniHeaderWrapper.BYTE_SIZE;
        }

        switch(header.elementType){
            case 0:
                console.error("INT Grid not yet supported!");
                return null;
            case 1:
                tex = new FloatGrid(header.size, header.sizeT, boundary);
                tex.load(new Float32Array(data, byteSize));
                break;
            case 2:
                tex = header.bytesPerElement == 12 ? new Vec3Grid(header.size, header.sizeT, boundary) : new Vec4Grid(header.size, header.sizeT, boundary);
                tex.load(new Float32Array(data, byteSize));
                break;
        }
        if (headerOut) {
            header.copy(headerOut);
        }
        return <T>tex;
    }
    static createTextureWithConstCol(col : vec4) {
        let tex = WebGL2.context.createTexture();

        WebGL2.context.bindTexture(WebGL2RenderingContext.TEXTURE_2D, tex);
        WebGL2.context.texParameteri(WebGL2RenderingContext.TEXTURE_2D, WebGL2RenderingContext.TEXTURE_MIN_FILTER, WebGL2.context.NEAREST);
        WebGL2.context.texParameteri(WebGL2RenderingContext.TEXTURE_2D, WebGL2RenderingContext.TEXTURE_MAG_FILTER, WebGL2.context.NEAREST);
        WebGL2.context.texImage2D(WebGL2RenderingContext.TEXTURE_2D, 0, WebGL2RenderingContext.RGBA8, 1, 1, 0, WebGL2RenderingContext.RGBA, WebGL2RenderingContext.UNSIGNED_BYTE, new Uint8Array(col.copy().scale(255).toArray()));
        WebGL2.context.bindTexture(WebGL2.context.TEXTURE_2D, null);

        return tex;
    }
    static createTextureFromArray(arr : number[]) : WebGLTexture {
        let tex = WebGL2.context.createTexture();
        let width = Math.sqrt(arr.length/4);

        WebGL2.context.bindTexture(WebGL2RenderingContext.TEXTURE_2D, tex);
        WebGL2.context.texParameteri(WebGL2RenderingContext.TEXTURE_2D, WebGL2RenderingContext.TEXTURE_MIN_FILTER, WebGL2.context.LINEAR);
        WebGL2.context.texParameteri(WebGL2RenderingContext.TEXTURE_2D, WebGL2RenderingContext.TEXTURE_MAG_FILTER, WebGL2.context.LINEAR);
        WebGL2.context.texParameteri(WebGL2RenderingContext.TEXTURE_2D, WebGL2RenderingContext.TEXTURE_WRAP_R, WebGL2RenderingContext.CLAMP_TO_EDGE);
        WebGL2.context.texParameteri(WebGL2RenderingContext.TEXTURE_2D, WebGL2RenderingContext.TEXTURE_WRAP_S, WebGL2RenderingContext.CLAMP_TO_EDGE);
        WebGL2.context.texParameteri(WebGL2RenderingContext.TEXTURE_2D, WebGL2RenderingContext.TEXTURE_WRAP_T, WebGL2RenderingContext.CLAMP_TO_EDGE);
        WebGL2.context.texImage2D(WebGL2RenderingContext.TEXTURE_2D, 0, WebGL2RenderingContext.RGBA8, width, width, 0, WebGL2RenderingContext.RGBA, WebGL2RenderingContext.UNSIGNED_BYTE, new Uint8Array(arr));
        WebGL2.context.bindTexture(WebGL2.context.TEXTURE_2D, null);

        return tex;
    }
