
import { RenderEngine } from "./renderers/RenderEngine";
import { GridBase } from "./resources/GridBase";
import { WebGL2 } from "./WebGL2";

export class EngineInitializer extends WebGL2 {
    private static mRequiredExtensions = [
        'OES_texture_float_linear',
        'EXT_color_buffer_float'
    ];

    static init(canvasID : string) : boolean {
        WebGL2.canvas = <HTMLCanvasElement>document.getElementById(canvasID);
        let ctx = WebGL2.canvas.getContext('webgl2');
        if ( ctx === null ) {
            console.error( 'Error creating WebGL2 context.' );
            return false;
        }
        
        WebGL2.context =  <WebGL2RenderingContext> ctx;

        WebGL2.MAX_2D_TEX_SIZE = WebGL2.context.getParameter(WebGL2RenderingContext.MAX_3D_TEXTURE_SIZE);
        WebGL2.MAX_3D_TEX_SIZE = WebGL2.context.getParameter(WebGL2RenderingContext.MAX_3D_TEXTURE_SIZE);
        WebGL2.MAX_DRAW_BUFFERS = WebGL2.context.getParameter(WebGL2RenderingContext.MAX_DRAW_BUFFERS);

        for(let ext of EngineInitializer.mRequiredExtensions) {
            if(!WebGL2.context.getExtension(ext)) {
                WebGL2.context = null;
                console.log("Browser/Device doesn't support a required extension!: " + ext)
                return false;
            }
        }

        RenderEngine._init();
        GridBase._init();
        return true;
    }
}