import { RenderComponent } from "./RenderComponent";
import { ParticleSystem } from "../../resources/ParticleSystem";
import * as vsParticle from '../../shaders/particle_vs.glsl'
import * as fsParticle from '../../shaders/particle_fs.glsl'
import { UniformBuffer } from "../../resources/UniformBuffer";
import { StructEntry, DataTypes } from "../../utils/StructBuffer";
import { TypedArray } from "../../utils/MathHelper";
import { WebGL2 } from "../../WebGL2";
import { ResourceFactory } from "../../utils/ResourceFactory";
import { ResourceLocation } from "../../utils/ResourceLocation";
import { RenderEngine } from "../../renderers/RenderEngine";
import { Mesh, IVertexAttribInfo } from "../../resources/Mesh";
import { MeshGenerator } from "../../utils/MeshGenerator";
import { GLHelper } from "../../utils/GLHelper";


export class ParticleRenderComponent extends RenderComponent {
    private static mParticleProgram : WebGLProgram;
    
    private static M_MATRIX_LOC : WebGLUniformLocation;
    private static INV_M_MATRIX_LOC : WebGLUniformLocation;
    private static PARTICLE_SCALE_LOC : WebGLUniformLocation;
    private static MAT_UBO_LOC : ResourceLocation<number>;

    private static CAM_UBO_LOC : number;
    private static SKY_TEX_LOC : WebGLUniformLocation;

    private mParticleSystem : ParticleSystem;
    private mMaterial : UniformBuffer;

    private static mParticleMesh : Mesh;

    static readonly OFF_ATTR: IVertexAttribInfo = {
        handle: 4,
        elem_size: 3
    };

    constructor(particleSystem : ParticleSystem) {
        super();

        this.mParticleSystem = particleSystem;

        this.mMaterial = new UniformBuffer([
            new StructEntry("reflCol", DataTypes.FLOAT32, 4, [0,0,0,0.0]),
            new StructEntry("diffCol", DataTypes.FLOAT32, 4, [1.0,1.0,0.0,1.0]),
            new StructEntry("specCol", DataTypes.FLOAT32, 4, [1.0,1.0,1.0,0.0]),
            new StructEntry("shinniness", DataTypes.INT32, 1, 16),
            new StructEntry("_pad", DataTypes.INT32, 3)
        ]);
    }

    public static _init() : void {
        ParticleRenderComponent.mParticleProgram = ResourceFactory.createShaderFromSource([
            { type: WebGL2.context.VERTEX_SHADER, code: vsParticle.default },
            { type: WebGL2.context.FRAGMENT_SHADER, code: fsParticle.default }
        ]);

        ParticleRenderComponent.M_MATRIX_LOC = WebGL2.context.getUniformLocation(ParticleRenderComponent.mParticleProgram, "model");
        ParticleRenderComponent.INV_M_MATRIX_LOC = WebGL2.context.getUniformLocation(ParticleRenderComponent.mParticleProgram, "inv_m");
        ParticleRenderComponent.PARTICLE_SCALE_LOC = WebGL2.context.getUniformLocation(ParticleRenderComponent.mParticleProgram, "radius");
        ParticleRenderComponent.CAM_UBO_LOC = WebGL2.context.getUniformBlockIndex(ParticleRenderComponent.mParticleProgram, "cam_data");
        ParticleRenderComponent.MAT_UBO_LOC = new ResourceLocation(WebGL2.context.getUniformBlockIndex(ParticleRenderComponent.mParticleProgram, "material"));
        ParticleRenderComponent.SKY_TEX_LOC = WebGL2.context.getUniformLocation(ParticleRenderComponent.mParticleProgram, "sky");

        ParticleRenderComponent.mParticleMesh = MeshGenerator.getPlaneMesh();
    }

    getParticleSystem() : ParticleSystem {
        return this.mParticleSystem;
    }

    setMaterialEntry(name : string, val : number|number[]|TypedArray){
        this.mMaterial.set(name, val);
    }

    draw() : void {
        WebGL2.context.useProgram(ParticleRenderComponent.mParticleProgram);
        this.mMaterial.uploadBuffer(ParticleRenderComponent.MAT_UBO_LOC.pnt);

        GLHelper.CheckGLError();
        WebGL2.context.uniformBlockBinding(ParticleRenderComponent.mParticleProgram, ParticleRenderComponent.MAT_UBO_LOC.loc, ParticleRenderComponent.MAT_UBO_LOC.pnt);
        WebGL2.context.uniformBlockBinding(ParticleRenderComponent.mParticleProgram, ParticleRenderComponent.CAM_UBO_LOC, RenderEngine.CAM_BUFFER_PNT);

        GLHelper.CheckGLError();
        WebGL2.context.uniformMatrix4fv(ParticleRenderComponent.M_MATRIX_LOC, false, this.mEntity.getTransform().getLocalToWorld().toArray());
        WebGL2.context.uniformMatrix4fv(ParticleRenderComponent.INV_M_MATRIX_LOC, false, this.mEntity.getTransform().getWorldToLocal().toArray());
        WebGL2.context.uniform1f(ParticleRenderComponent.PARTICLE_SCALE_LOC, this.mParticleSystem.scale());

        WebGL2.context.uniform1i(ParticleRenderComponent.SKY_TEX_LOC, RenderEngine.SKY_TEX_PNT);

        this.mParticleSystem.bind();

        GLHelper.CheckGLError();

        WebGL2.context.enableVertexAttribArray(ParticleRenderComponent.OFF_ATTR.handle);
        WebGL2.context.vertexAttribPointer(ParticleRenderComponent.OFF_ATTR.handle, ParticleRenderComponent.OFF_ATTR.elem_size, WebGL2RenderingContext.FLOAT, false, ParticleRenderComponent.OFF_ATTR.elem_size * 4, 0);
        WebGL2.context.vertexAttribDivisor(ParticleRenderComponent.OFF_ATTR.handle, 1);
        GLHelper.CheckGLError();

        ParticleRenderComponent.mParticleMesh.drawInstanced(this.mParticleSystem.count());
    }
}