import { EngineInitializer } from '../../../WebGL2Engine/src/EngineInitializer';
import { CameraComponent } from '../../../WebGL2Engine/src/scene/components/CameraComponent';
import { Entity } from '../../../WebGL2Engine/src/scene/Entity';
import { VolumeRenderComponent } from '../../../WebGL2Engine/src/scene/components/VolumeRenderComponent';
import { ParticleRenderComponent } from '../../../WebGL2Engine/src/scene/components/ParticleRenderComponent';
import { Scene } from '../../../WebGL2Engine/src/scene/Scene';
import { GLHelper } from '../../../WebGL2Engine/src/utils/GLHelper';
import { ResourceFactory } from '../../../WebGL2Engine/src/utils/ResourceFactory';
import vec3 from '../../../WebGL2Engine/src/utils/tsm/vec3';
import vec4 from '../../../WebGL2Engine/src/utils/tsm/vec4';
import { LightComponent } from '../../../WebGL2Engine/src/scene/components/LightComponent';

import $ from "jquery";

export default class ViewerScene extends Scene {
    mRenderComps : (VolumeRenderComponent | ParticleRenderComponent)[] = [];
    mRenderEntity : Entity;
    mRepresentations : string[] = [];

    mAzimut = 0;
    mInclination = 90;
    mCamDistance = 2.0;
    mouseStateDown : boolean = false;
  
    oldX : number;
    oldY : number;

    constructor(canvasID : string, sky? : number[][]) {
        super();
        EngineInitializer.init(canvasID);

        const camera = this.createEntity();

        let cubeTex : WebGLTexture;

        if (sky) {
            cubeTex = ResourceFactory.createCubeTextureFromArray(sky);
        } else {
            cubeTex = ResourceFactory.createCubeTextureFromConstCol(new vec4([0.7,0.7,0.7,1]));
        }        

        camera.setCameraComponent(
            new CameraComponent(
                cubeTex,            
                60,   // fovy
                0.1,  // near
                10    // far
            )
        );
        this.updateCam();
        const light = this.createEntity();

        light.setLightComponent(new LightComponent(new vec3([1, 1, 1])));
        light.getTransform().setPosition(new vec3([1, 2, -2]));
        light.getTransform().setScale(new vec3([0.1, 0.1, 0.1]));
    
        this.mRenderEntity = this.createEntity();
        this.mRenderEntity.getTransform().setPosition(new vec3([0, 0.25, 0]));

        this.init();

        let th = this;       

        let canvas = $('#' + canvasID);
        canvas.mousedown(function(e : any) {
          th.mouseStateDown = true;
          th.oldX = null;
        });
        document.onmouseup = function(e : any) {
          th.mouseStateDown = false;
        };
        canvas.mousemove(function(e : any) {
          if(th.mouseStateDown) {
            th.duringDrag(e.pageX, e.pageY);
            th.update(0.1);
          }
        });
        canvas.on("wheel", function(e : any) {
          e.preventDefault();
          th.mCamDistance += 0.03 * (<WheelEvent>e.originalEvent).deltaY;
          th.mCamDistance = Math.min(Math.max(th.mCamDistance, 1.2), 4.0);
          th.update(0.1);
        });
        this.update(0.1);
    }
    init() : void { }

    duringDrag(x : number, y : number) {
        if (!this.oldX) {
        this.oldX = x;
        this.oldY = y;
        }
        this.mAzimut -= x - this.oldX;
        this.mInclination -= y - this.oldY;

        if(this.mAzimut < -180) {
        this.mAzimut += 360;
        } else if(this.mAzimut > 180) {
        this.mAzimut -= 360;
        }
        if(this.mInclination < 10) {
        this.mInclination = 10;
        } else if(this.mInclination > 90) {
        this.mInclination = 90;
        }

        this.oldX = x;
        this.oldY = y;
    }
    
    updateCam(){
        let az_rad = this.mAzimut/180 * Math.PI;
        let inc_rad = this.mInclination/180 * Math.PI;
        let pos = new vec3([
          this.mCamDistance * Math.sin(inc_rad) * Math.sin(az_rad),
          this.mCamDistance * Math.cos(inc_rad),
          this.mCamDistance * Math.sin(inc_rad) * Math.cos(az_rad)
        ]);
        this.mCamera.getEntity().getTransform().setPosition(pos);
        this.mCamera.getEntity().getTransform().lookAt(new vec3([0, 0, 0]));
    }

    createRenderComponent(representation_type = "DENSITY", dim? : number[]) {
        switch(representation_type) {
            case "DENSITY":
            case "SDF":
                return new VolumeRenderComponent(ResourceFactory.createFloatGrid(new vec3([dim[0], dim[1], dim[2]]), 1, new vec3([1,1,1])));
            case "PARTICLE":
                return new ParticleRenderComponent(ResourceFactory.createParticleSystemFromArray(new Float32Array([0,0,0]), 0.1));
            default:
                console.error("Representation type not recognized! Expected one of these: DENSITY, SDF, PARTICLE!")
        }
        return undefined;
    }

    flattenDeep(arr1 : any) {
        return arr1.reduce((acc: any, val: any) => Array.isArray(val) ? acc.concat(this.flattenDeep(val)) : acc.concat(val), []);
    }

    setData(value : number[][][][], representation_type? : string|string[], mat? : string, idx? : number, scale? : number) : void {
        if(representation_type === undefined) {
            representation_type = "DENSITY";
        }

        for (let i = 0; i < value.length; i++) {
            let v = value[i];

            let r = representation_type instanceof Array ? representation_type[i] : representation_type;
            let dim = r != "PARTICLE" ? [v.length, v[0].length, v[0][0].length] : undefined;
            if(this.mRepresentations.length <= i){
                this.mRepresentations.push(r);
                this.mRenderComps.push(this.createRenderComponent(r, dim));
            } else if(r != this.mRepresentations[i]) {
                this.mRepresentations[i] = r;
                this.mRenderComps[i] = this.createRenderComponent(r, dim);
            } else if (dim != undefined && r != "PARTICLE") {
                let v = (<VolumeRenderComponent>this.mRenderComps[i]).getVolumeTexture().getSize();
                if(dim[0] != v.x || dim[1] != v.y || dim[2] != v.z) {
                    this.mRenderComps[i] = this.createRenderComponent(r, dim);
                }
            }

            console.log("Load data: " + (i+1) + "/" + value.length + ", " + r + " " + dim);

            switch(r) {
                case "DENSITY":
                    (<VolumeRenderComponent>this.mRenderComps[i]).getVolumeTexture().load(new Float32Array(<number[]>this.flattenDeep(v)));
                    (<VolumeRenderComponent>this.mRenderComps[i]).getVolumeTexture().multConst(-1.0);
                    (<VolumeRenderComponent>this.mRenderComps[i]).getVolumeTexture().addConst(0.1);
                    break;
                case "SDF":            
                    (<VolumeRenderComponent>this.mRenderComps[i]).getVolumeTexture().load(new Float32Array(<number[]>this.flattenDeep(v)));
                    break;
                case "PARTICLE":
                    (<ParticleRenderComponent>this.mRenderComps[i]).getParticleSystem().load(new Float32Array(<number[]>this.flattenDeep(v)));
                    break
            }
        }
        this.setMaterial(mat);
        this.setIdx(idx);
        this.setScale(scale);
    }

    setIdx(idx? : number) {
        if(idx === undefined) {
            idx = 0;
        }
        if (idx < this.mRenderComps.length) {
            this.mRenderEntity.setRenderComponent(this.mRenderComps[idx]);
        }
    }

    setMaterial(mat? : string) {
        if (mat === undefined) {
            mat = "SOLID";
        }
        switch(mat) {
            case "SMOKE":
            case "DARK_SMOKE":
                for(let i = 0; i < this.mRenderComps.length; i++){
                    let rc = this.mRenderComps[i];
                    rc.setMaterialEntry("diffCol", [0.0,0.0,0.0,0.0]);
                    rc.setMaterialEntry("reflCol", [1, 1, 1, 0.0]);
                    rc.setMaterialEntry("specCol", [1, 1, 1, 0.0]);
                    if (this.mRepresentations[i] != "PARTICLE") {
                        rc.setMaterialEntry("volCol", [0.0,0.0,0.0,0.5]);
                        rc.setMaterialEntry("dens_fac", 10);
                    }
                }
                break;
            case "LIGHT_SMOKE":
                for(let i = 0; i < this.mRenderComps.length; i++){
                    let rc = this.mRenderComps[i];
                    rc.setMaterialEntry("diffCol", [0.0,0.0,0.0,0.0]);
                    rc.setMaterialEntry("reflCol", [1, 1, 1, 0.0]);
                    rc.setMaterialEntry("specCol", [1, 1, 1, 0.0]);
                    if (this.mRepresentations[i] != "PARTICLE") {
                        rc.setMaterialEntry("volCol", [1.0,1.0,1.0,0.1]);
                        rc.setMaterialEntry("dens_fac", 10);
                    }
                }
                break;
            case "LIQUID":
                for(let i = 0; i < this.mRenderComps.length; i++){
                    let rc = this.mRenderComps[i];
                    rc.setMaterialEntry("diffCol", [0.0,0.0,0.0,0.0]);
                    rc.setMaterialEntry("reflCol", [1, 1, 1, 1.0]);
                    rc.setMaterialEntry("specCol", [1, 1, 1, 1.0]);
                    if (this.mRepresentations[i] != "PARTICLE") {
                        rc.setMaterialEntry("volCol", [0.0,0.0,0.0,0.0]);
                        rc.setMaterialEntry("dens_fac", 0);
                    }
                }
                break;
            case "SOLID":
                for(let i = 0; i < this.mRenderComps.length; i++){
                    let rc = this.mRenderComps[i];
                    rc.setMaterialEntry("diffCol", [0.1,0.47,0.8,1.0]);
                    rc.setMaterialEntry("reflCol", [1, 1, 1, 0.0]);
                    rc.setMaterialEntry("specCol", [1, 1, 1, 0.0]);
                    if (this.mRepresentations[i] != "PARTICLE") {
                        rc.setMaterialEntry("volCol", [0.0,0.0,0.0,0.0]);
                        rc.setMaterialEntry("dens_fac", 100);
                    }
                }
                break;
            default:
                console.error("Material type not recognized! Expected one of these: SMOKE, DARK_SMOKE, LIGHT_SMOKE, LIQUID, SOLID!")
                break;
        }
    }

    setScale(scale? : number) {
        if(scale === undefined) {
            scale = 0.1;
        }
        for (let i = 0; i < this.mRepresentations.length; i++) {
            if (this.mRepresentations[i] == "PARTICLE") {
                (<ParticleRenderComponent>this.mRenderComps[i]).getParticleSystem().setScale(scale);
            }
        }
    }

    update(dt: number) : void {
        this.updateCam();
        this.render();
        GLHelper.finish();
    }
}
